import {Component, HostBinding, Input} from "@angular/core";
import {DiskManagementActionAvailabilityService} from "../validation/disk-management-action-availability.service";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ActionAvailableStatus} from "@util/validation/action-available-status";
import {DiskManagementActionContext} from "../validation/disk-management-action-context";
import {DiskGroupData} from "@generated/disk-group-data";
import {HostData} from "@generated/host-data";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {DiskGroupActionContext} from "@navigation/context/disk-group-action-context";
import {HostActionData} from "@model/host-action.data";
import {DiskGroupActionData} from "@model/disk-group-action.data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {EvacuationContext} from "@navigation/context/evacuation.context";
import {VsanUiUtils} from "@util/vsan-util";
import {PreCheckEvacuationDialogSize} from "@component/precheck/pre-check-evacuation-dialog-size";
import {ViewObjectsContext} from "../model/view-objects-context";

@Component({
   selector: "vsan-storage-group-actions",
   templateUrl: "storage-group-actions.html",
   styleUrls: ["storage-group-actions.scss"],
})
export class StorageGroupActionsComponent {

   @HostBinding("class")
   hostClass: string = "vsan-dropdown-actions";

   @Input()
   autoClaimMode: boolean;

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   @Input()
   hasHostMaintenancePermission: boolean;

   @Input()
   selectedDiskGroup: DiskGroupData;

   @Input()
   selectedHost: HostData;

   DiskManagementActionAvailabilityService: typeof DiskManagementActionAvailabilityService =
         DiskManagementActionAvailabilityService;

   constructor(private navigationService: NavigationService,
         private diskManagementActionAvailabilityService: DiskManagementActionAvailabilityService) {}

   actionDisabled = (actionId: string): boolean => {
      return !this.getActionStatus(actionId).available;
   };

   getActionButtonTitle = (actionId: string): string => {
      return this.getActionStatus(actionId).message;
   };

   private getActionStatus = (actionId: string): ActionAvailableStatus => {
      return this.diskManagementActionAvailabilityService.checkAvailability(this.getActionValidationContext(actionId));
   };

   private getActionValidationContext(actionId: string): DiskManagementActionContext {
      return {
         actionId: actionId,
         hasEditPermission: this.hasEditPermission,
         vsanConfig: null,
         isEmptyCluster: null,
         autoClaimMode: this.autoClaimMode,
         diskGroup: this.selectedDiskGroup,
         host: this.selectedHost,
         storageData: null,
         clusterCapabilities: this.clusterCapabilities,
         hasHostMaintenancePermission: this.hasHostMaintenancePermission,
         spaceEfficiencyConfig: null,
      };
   }

   get isVsanDiskGroup(): boolean {
      return DiskMgmtDiskGroupUtil.isVsanDiskGroup(this.selectedDiskGroup);
   }

   get isPmemDiskGroup(): boolean {
      return DiskMgmtDiskGroupUtil.isPmemDiskGroup(this.selectedDiskGroup);
   }

   get precheckUnavailable(): boolean {
      return this.actionDisabled(DiskManagementActionAvailabilityService.PRE_CHECK_ID);
   }

   get canBeRecreated(): boolean {
      return this.clusterCapabilities
            && this.clusterCapabilities.isRecreateDiskGroupSupported
            && !this.autoClaimMode;
   }

   getPrecheckLabel(): string {
      return DiskMgmtDiskGroupUtil.getDataMigrationPrecheckLabel(this.clusterCapabilities);
   }

   viewGroupObjects = (): void => this.diskManagementActionAvailabilityService.objectsViewSelected
         .next(new ViewObjectsContext(this.selectedHost, this.selectedDiskGroup, null));

   showViewGroupObjectsAction = (): boolean => !DiskMgmtDiskGroupUtil.isUnusedDiskGroup(this.selectedDiskGroup) &&
         this.clusterCapabilities.isObjectIdentitiesSupported;

   goToPrecheckEvacuation = (): void => {
      const clusterRefUid: string = ManagedObject.contextObjectUid;

      // Use the cache disk's uuid as group's uuid.
      const objUuid: string = this.selectedDiskGroup.disks.find(d => d.isMappedAsCache).vsanUuid;

      if (this.clusterCapabilities.isDiskResourcePrecheckSupported) {
         const evacuationContext: EvacuationContext =
               VsanDiskMgmtActionsUtil.getEvacuationContext(this.selectedDiskGroup.ownerHostRef, objUuid);
         this.navigationService.navigateTo(PlatformExtension.CLUSTER_DATA_MIGRATION_PRE_CHECK,
               clusterRefUid, evacuationContext);
         return;
      }
      const context: any = {
         hostMor: this.selectedDiskGroup.ownerHostRef,
         objectName: VsanUiUtils.getString("vsan.dialog.precheckEvacuation.subtitle", this.selectedDiskGroup.name),
         objectUid: objUuid,
      };

      new ModalBuilder("preCheckEvacuation")
            .setSize(PreCheckEvacuationDialogSize.WIDTH,
                  PreCheckEvacuationDialogSize.HEIGHT_WITH_OBJECTS)
            .open(context);
   };

   addDisks = (): void => {
      new ModalBuilder("diskManagement/addDisk")
            .open(this.selectedDiskGroup)
            .then((task: ManagedObject) => this.diskManagementActionAvailabilityService.taskStarted.next([task]));
   };

   delete = (): void => {
      const height: number = this.precheckUnavailable ?
            VsanDiskMgmtActionsUtil.DELETE_DISK_GROUP_HEIGHT_SMALL :
            VsanDiskMgmtActionsUtil.DELETE_DISK_GROUP_HEIGHT_MEDIUM;

      new ModalBuilder("diskManagement/deleteDiskGroup")
            .setSize(VsanDiskMgmtActionsUtil.DELETE_DISK_GROUP_WIDTH, height)
            .open(this.getActionContext())
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   };

   recreate = (): void => {
      const height: number = this.precheckUnavailable
            ? VsanDiskMgmtActionsUtil.RECREATE_DIALOG_HEIGHT_SMALL
            : VsanDiskMgmtActionsUtil.RECREATE_DIALOG_HEIGHT_MEDIUM;
      new ModalBuilder("diskManagement/recreateDiskGroup")
            .setSize(VsanDiskMgmtActionsUtil.RECREATE_DIALOG_WIDTH, height)
            .open(this.getActionContext())
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   };

   mount = (): void => {
      new ModalBuilder("diskManagement/mountDiskGroup")
            .setSize(VsanDiskMgmtActionsUtil.MOUNT_DISK_GROUP_DIALOG_WIDTH,
                  VsanDiskMgmtActionsUtil.MOUNT_DISK_GROUP_DIALOG_HEIGHT)
            .open(this.getActionContext())
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   };

   unmount = (): void => {
      const height: number = this.precheckUnavailable
            ? VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_SMALL
            : VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_MEDIUM;
      new ModalBuilder("diskManagement/unmountDiskGroup")
            .setSize(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_WIDTH, height)
            .open(this.getActionContext())
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   };

   stopManagingPmem(): void {
      new ModalBuilder("diskManagement/stopManagingPmem")
            .setSize(VsanDiskMgmtActionsUtil.STOP_MANAGING_PMEM_WIDTH,
                  VsanDiskMgmtActionsUtil.STOP_MANAGING_PMEM_HEIGHT)
            .open()
            .then((task: ManagedObject) => this.diskManagementActionAvailabilityService.taskStarted.next([task]));
   }

   private getActionContext = (): DiskGroupActionContext => {
      return {
         host: HostActionData.fromHostData(this.selectedHost),
         diskGroup: DiskGroupActionData.fromDiskGroupData(this.selectedDiskGroup),
      };
   };
}