import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {HostData} from "@generated/host-data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {LoadingAwareProperty} from "@model/loading-aware-property";
import {NavigationService} from "@navigation/navigation.service";
import {VsanUiUtils} from "@util/vsan-util";
import {DiskMgmtEntitiesSummaryService, DiskMgmtEntitySummary} from "./disk-mgmt-entities-summary-service";
import {DiskManagementActionAvailabilityService} from "./validation/disk-management-action-availability.service";

/**
 * The component is used as overview of disk management view. It shows the number of hosts, disk groups and
 * different types of disks per cluster or host level. Cluster / host level specific actions are provided in the footer.
 */
@Component({
   templateUrl: "disk-mgmt-summary.html",
   selector: "vsan-disk-mgmt-summary",
   styleUrls: ["disk-mgmt-summary.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DiskMgmtSummaryComponent {

   @Input()
   clusterName: string;

   /**
    * Represents the index of the host from the hostList. When the index:
    *    - equals to -1 the component will aggregate data on cluster level
    *    - is equal or greater than 0 the component will aggregate data on a host level.
    */
   _hostIndex: number;
   @Input()
   get hostIndex(): number {
      return this._hostIndex;
   }

   set hostIndex(value: number) {
      if (VsanUiUtils.isUnset(value) || value === this._hostIndex) {
         return;
      }
      this._hostIndex = value;
      if (!VsanUiUtils.isEmpty(this.hostList)) {
         this.setItemsCount();
      }
   }

   _hostList: HostData[];
   @Input()
   get hostList(): HostData[] {
      return this._hostList;
   }

   set hostList(value: HostData[]) {
      if (VsanUiUtils.isUnset(value)) {
         return;
      }
      this._hostList = value;
      this.setItemsCount();
   }

   @Input()
   autoClaimMode: boolean;

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   @Input()
   vsanConfig: VsanConfigSpec;

   @Input()
   hasHostMaintenancePermission: boolean;

   @Input()
   hasDuplicatePciIdDevices: LoadingAwareProperty<boolean>;

   DiskManagementActionAvailabilityService: typeof DiskManagementActionAvailabilityService =
         DiskManagementActionAvailabilityService;

   summaryEntities: DiskMgmtEntitySummary[];

   constructor(private navigationService: NavigationService,
         private diskManagementActionAvailabilityService: DiskManagementActionAvailabilityService,
         private diskMgmtEntitiesSummaryService: DiskMgmtEntitiesSummaryService) {
   }

   private setItemsCount(): void {
      const hostsData: HostData[] = this.isHostDetailsLevel() ? [this.getHost()] : this.hostList;
      this.summaryEntities = this.diskMgmtEntitiesSummaryService.createEntities(this.isHostDetailsLevel(), hostsData);
   }

   isHostDetailsLevel(): boolean {
      return this.hostIndex > -1;
   }

   getHost(): HostData {
      return this.isHostDetailsLevel() ? this.hostList[this.hostIndex] : null;
   }
}