import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Logger} from "@util/logger";

@Component({
   templateUrl: "pre-check-issues.html",
   styleUrls: ["pre-check-issues.scss"],
   selector: "vsan-diskmgmt-pre-check-issues",
})
export class PreCheckIssuesComponent implements OnInit {

   subtitle: string;
   issues: string[];

   ngOnInit(): void {
      const context: any = VsanUiUtils.getModalContext();
      if (context) {
         this.subtitle = context.clusterName;
         this.issues = context.issues;
      } else {
         Logger.error("Unable to get modal context");
      }
   }
}