import {Component} from "@angular/core";
import {ScsiDisk} from "@component/diskmgmt/model/scsi-disk.data";
import {DecommissionModeSummary} from "@component/precheck/pre-check-evacuation-summary.component";
import {DecommissionMode} from "@generated/decommission-mode";
import {DiskData} from "@generated/disk-data";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {RemoveDiskGroupSpec} from "@generated/remove-disk-group-spec";
import {RemoveDiskSpec} from "@generated/remove-disk-spec";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {VsanPropertyProvider} from "@generated/vsan-property-provider";
import {VsanQueryDataEvacuationInfoSpec} from "@generated/vsan-query-data-evacuation-info-spec";
import {DiskGroupActionData} from "@model/disk-group-action.data";
import {HostActionData} from "@model/host-action.data";
import {DeleteDisksContext} from "@navigation/context/delete-disks-context";
import {EvacuationContext} from "@navigation/context/evacuation.context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {HostSystem} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {Logger} from "@util/logger";
import {VsanDataMigrationUtil} from "@util/vsan-data-migration-util";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {VsanDiskUtil} from "@util/vsan-disk-util";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "delete-disks.html",
   styleUrls: ["../actions-common.scss"],
   selector: "vsan-diskmgmt-delete-disks",
})
export class DeleteDisksComponent {
   subtitle: string;
   warningMessages: string[] = [];
   footerText: string;
   dataOnDiskLabel: string = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabel");
   shouldAllowDataMigration: boolean;
   selectedDecommissionMode: DecommissionMode = DecommissionMode.ensureObjectAccessibility;

   @LoadingIndicator({
      name: [LoaderName.ON_INIT, LoaderName.DATA_EVACUATION, LoaderName.ON_CONFIRM],
   })
   busy: boolean;

   alerts: string[];
   isValidDecommisionMode: boolean = true;
   goToPrecheckButtonText: string;
   host: HostActionData;
   disks: DiskData[];
   preselectedDecommissionMode: DecommissionMode;
   localizedPreselectedDecommissionMode: string;
   isVsanMaxEnabled: boolean;

   private diskGroups: DiskGroupActionData[];
   isDiskResourcePrecheckSupported: boolean = true;
   private isAbsentDisk: boolean = false;

   constructor(private vsanPropertyProvider: VsanPropertyProvider,
         private diskGroupMutationService: DiskGroupMutationService,
         private navigationService: NavigationService,
         private dataService: DataServiceClient) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.dialog.deleteDiskFromDiskGroup.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      const context: DeleteDisksContext = VsanUiUtils.getModalContext();
      this.host = context.host;
      this.disks = context.disks;
      this.diskGroups = context.diskGroups;
      this.isVsanMaxEnabled = context.isVsanMaxEnabled;
      this.isAbsentDisk = VsanDiskUtil.isAbsentDisk(this.disks[0].diskStatus);
      if (this.isAbsentDisk) {
         // We can only remove the absent disk with noAction mode.
         this.preselectedDecommissionMode = DecommissionMode.noAction;
      } else {
         this.preselectedDecommissionMode = context.decommissionMode;
      }
      this.localizedPreselectedDecommissionMode =
            VsanDataMigrationUtil.getLocalizedDecommissionMode(this.preselectedDecommissionMode);

      this.host.isInMaintenanceMode = await this.dataService
            .getProperty(HostSystem.runtime.inMaintenanceMode, this.host.hostRef);
      this.initUI();
   }

   @ErrorHandler({name: LoaderName.ON_INIT})
   private handleInitError(): void {
      this.alerts = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
   }

   private initUI(): void {
      this.shouldAllowDataMigration = this.shouldAllowDisksDataMigration();
      if (!this.shouldAllowDataMigration) {
         this.selectedDecommissionMode = DecommissionMode.noAction;
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.DELETE_DISKS_HEIGHT_SMALL);
      }

      this.warningMessages.push(this.getHeaderMessage());
      const dataMigrationWarning: string = this.getDataMigrationWarning(this.disks, this.host.isInMaintenanceMode);
      if (dataMigrationWarning) {
         this.warningMessages.push(dataMigrationWarning);
      }

      this.showForDisks();
   }

   private shouldAllowDisksDataMigration = (): boolean => {
      const isHostEligible: boolean = this.host.isWhatIfSupported // feature is supported
            && !this.host.isWitnessHost // not on a withness host
            && !this.host.isInMaintenanceMode; // Not on a host in MM

      return this.isVsanMaxEnabled
            ? this.isDataMigrationAllowedForVsanMax(isHostEligible)
            : this.isDataMigrationAllowedForVsan(isHostEligible);
   };

   private isDataMigrationAllowedForVsanMax(isHostEligible: boolean): boolean {
      return isHostEligible && this.disks.every(disk => VsanDiskUtil.isVsanDiskMounted(disk));
   }

   private isDataMigrationAllowedForVsan(isHostEligible: boolean): boolean {
      return isHostEligible
            && this.diskGroups.some(group => group.isMounted) // the selected disks/group are mounted
            && this.diskGroups.length === 1 && this.disks.length <= 1 // only for single/no disk
            && this.diskGroups.some(group => !group.isLocked); // Not all groups are locked
   }

   private getHeaderMessage = (): string => {
      const groupUuids: string = this.diskGroups.map(group => group.vsanUuid).join(", ");
      const dataMigrationMessage: string = this.getDataMigrationMessage(this.disks, groupUuids);
      const removeAllCapacityDisksMessage: string = this.isRemovingAllCapacityDisks(this.diskGroups, this.disks) ?
            VsanUiUtils.getString("vsan.confirmAllDataDisksRemove.text") : "";

      if (this.host.isInMaintenanceMode) {
         return dataMigrationMessage;
      } else if (this.host.isWitnessHost) {
         return VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.externalWitnessHostWarning",
               dataMigrationMessage, removeAllCapacityDisksMessage);
      } else if (this.isAbsentDisk) {
         return VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.absentDisk",
               dataMigrationMessage, removeAllCapacityDisksMessage);
      } else {
         return VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.headerDataEvacuation",
               dataMigrationMessage, removeAllCapacityDisksMessage);
      }
   };

   private getDataMigrationMessage = (disks: DiskData[], groupUuids: string): string => {
      if (!VsanUiUtils.isEmpty(groupUuids)) {
         return disks.length === 1 ?
               VsanUiUtils.getString("vsan.confirmSingleDiskRemove.dataEvacuation.text", disks[0].name, groupUuids) :
               VsanUiUtils.getString("vsan.confirmMultipleDisksRemove.dataEvacuation.text", groupUuids);
      } else {
         return disks.length === 1 ?
               VsanUiUtils.getString("vsan.confirmSingleDiskRemove.dataEvacuation.noGroups.text", disks[0].name) :
               VsanUiUtils.getString("vsan.confirmMultipleDisksRemove.dataEvacuation.noGroups.text");
      }
   };

   private isRemovingAllCapacityDisks = (groups: DiskGroupActionData[], disks: DiskData[]): boolean => {
      const allCapacityDisks: ScsiDisk[] = [];
      groups.forEach(group => allCapacityDisks.push(...group.diskMapping.nonSsd));
      return disks.length === allCapacityDisks.length;
   };

   private getDataMigrationWarning = (disks: DiskData[], isInMaintenanceMode: boolean): string => {
      if (isInMaintenanceMode) {
         return this.isVsanMaxEnabled
               ? VsanUiUtils.getString("vsan.dialog.deleteDisk.hostInMaintenance.warning")
               : VsanUiUtils.getString("vsan.dialog.deleteDiskFromDiskGroup.hostInMaintenance.warning");
      } else {
         if (disks[0].deviceState === DiskData.DeviceState.OFF) {
            return VsanUiUtils.getString("vsan.dialog.deleteDiskFromDiskGroup.unmountedDisk.warning");
         }
      }
   };

   private showForDisks = () => {
      this.footerText = this.disks.length === 1 ?
            VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.footerRemoveDisk") :
            VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.footerRemoveDisks");

      switch (this.diskGroups.length) {
         case 0:
            this.subtitle = "";
            break;
         case 1:
            this.subtitle = this.diskGroups[0].vsanUuid;
            break;
         default:
            this.subtitle = VsanUiUtils.getString("vsan.dialog.deleteDiskFromDiskGroups.count",
                  this.diskGroups.length);
      }
      this.requestDataEvacuationProperty(this.disks.map(data => data.disk));
   };

   @Loader({name: LoaderName.DATA_EVACUATION})
   private async requestDataEvacuationProperty(disks: ScsiDisk[]): Promise<void> {
      if (!disks.every(DiskMgmtDiskUtil.isScsiDiskHealthy)) {
         this.dataOnDiskLabel = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabel.error");
         return;
      }

      const spec: VsanQueryDataEvacuationInfoSpec = VsanQueryDataEvacuationInfoSpec.Factory.create(disks);
      const result: number = await this.vsanPropertyProvider.getVsanDataEvacuationInfo(this.host.hostRef, spec);
      this.onDataEvacuationPropertyRetrieved(result);
   }

   private onDataEvacuationPropertyRetrieved = (result: number) => {
      if (result >= 0) {
         const formattedSize: string = VsanUiUtils.formatFileSize(result);
         this.dataOnDiskLabel = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabelWithSize",
               formattedSize);
      }
   };

   @ErrorHandler({name: LoaderName.DATA_EVACUATION})
   private onDataRetrievalError(message: any): void {
      this.alerts = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
      this.dataOnDiskLabel = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabel.error");
      Logger.error("Failed to retrieve vsanDataEvacuationInfo: " + message);
   }

   onDecommissionModeSummaryChanged = (summary: DecommissionModeSummary) => {
      this.selectedDecommissionMode = summary.value;
      this.isValidDecommisionMode = summary.isValidDecommissionMode;
      // change the dialog height based on the content
      if (VsanDiskMgmtActionsUtil.isPrecheckWithObjects(summary) && !this.isDiskResourcePrecheckSupported) {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.DELETE_DISKS_HEIGHT_LARGE);
      } else if (this.isDiskResourcePrecheckSupported) {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.DELETE_DISKS_HEIGHT_PRECHECK);
      } else {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.DELETE_DISKS_HEIGHT_MEDIUM);
      }
   };

   onDiskResourcePrecheckSupportedChanged = (isSupported: boolean) => {
      this.isDiskResourcePrecheckSupported = isSupported;
      // Preselecting decommmission mode means that this dialog is opened from Data migration pre-check page,
      // in which case GO TO PRE-CHECK button doesn't need to be display.
      this.goToPrecheckButtonText = !this.preselectedDecommissionMode && this.isDiskResourcePrecheckSupported ?
            VsanUiUtils.getString("vsan.actions.goToPrecheckDataMigration.label") : "";
   };

   @Loader({name: LoaderName.ON_CONFIRM})
   async onOkClicked(): Promise<void> {
      const decommissionMode: DecommissionMode = this.preselectedDecommissionMode ?
            this.preselectedDecommissionMode :
            VsanDiskMgmtActionsUtil.getSelectedDecommissionMode(
                  this.shouldAllowDataMigration, this.selectedDecommissionMode);
      let tasks: ManagedObject[] = [];
      if (this.isVsanMaxEnabled) {
         tasks.push(await this.deleteVsanMaxDisks(decommissionMode));
      } else {
         tasks.push(...await this.deleteVsanDisks(decommissionMode));
      }

      this.handleTaskList(tasks);
   }

   private deleteVsanDisks(decommissionMode: DecommissionMode): Promise<ManagedObject[]> {
      const disksToDelete: ScsiDisk[] = [];
      // Get disk group vsanUuid and its disks to be deleted.
      const diskGroupsToDelete: { [diskGroupVsanUuid: string]: VsanDiskMapping; } = {};

      this.disks.forEach((disk: DiskData) => {
         const group: DiskGroupActionData = this.diskGroups.find(diskGroup => diskGroup.vsanUuid === disk.diskGroup);
         const diskMapping: VsanDiskMapping = group.diskMapping;

         if (diskMapping.nonSsd.length === 1) {
            // We need to remove the whole disk group if the current disk is the last one capacity disk.
            diskGroupsToDelete[disk.diskGroup] = diskMapping;
         } else {
            // The disk is not the last capacity disk in the group, remove it separately.
            disksToDelete.push(disk.disk);
         }
      });

      const diskGroupSpec: RemoveDiskGroupSpec = RemoveDiskGroupSpec.Factory.create(
            decommissionMode,
            Object.keys(diskGroupsToDelete).map(key => diskGroupsToDelete[key]));
      const diskSpec: RemoveDiskSpec = RemoveDiskSpec.Factory.create(decommissionMode, disksToDelete);

      return this.diskGroupMutationService.removeDisksAndMappings(
            ManagedObject.contextObject, this.host.hostRef, diskGroupSpec, diskSpec);
   }

   private deleteVsanMaxDisks(decommissionMode: DecommissionMode): Promise<ManagedObject> {
      const vSanMaxDisksToDelete: string[] = this.disks.map(disk => disk.vsanUuid);

      return this.diskGroupMutationService.removeVsanMaxDisks(
            ManagedObject.contextObject, decommissionMode, vSanMaxDisksToDelete);
   }

   onGoToPrecheckClicked = () => {
      const evacuationContext: EvacuationContext = VsanDiskMgmtActionsUtil.getEvacuationContext(
            this.host.hostRef, this.disks[0].vsanUuid, this.selectedDecommissionMode);
      this.navigationService.navigateToAndCloseModal(PlatformExtension.CLUSTER_DATA_MIGRATION_PRE_CHECK,
            ManagedObject.contextObjectUid, evacuationContext);
   };

   private handleTaskList = (tasks: ManagedObject[]) => {
      VsanUiUtils.closeModalDialog(tasks);
   };

   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private onDeleteOperationError(message: any): void {
      this.alerts = [message];
   }

   get isDeleteButtonBlocked(): boolean {
      return this.busy || !this.isValidDecommisionMode;
   }
}
