import {ChangeDetectorRef, Component, OnInit} from "@angular/core";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {DiskManagementService} from "@generated/disk-management-service";
import {VsanClaimDisksData} from "@generated/vsan-claim-disks-data";
import {VsanClaimHostDiskMappingsSpec} from "@generated/vsan-claim-host-disk-mappings-spec";
import {VsanConfigService} from "@generated/vsan-config-service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {ArrayUtils} from "@util/array-util";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "claim-disks-dialog.html",
   selector: "vsan-claim-disks-dialog",
})
export class ClaimDisksDialogComponent implements OnInit {

   @LoadingIndicator()
   loading: boolean;

   @LoadingIndicator({name: LoaderName.ON_CONFIRM})
   inProgress: boolean;

   vsanConfigSpec: VsanConfigSpec;
   hostsClaimingData: Map<string, VsanClaimDisksData>;

   isConfigValid: boolean;
   claimDisksSpecs: VsanClaimHostDiskMappingsSpec[];
   alertMessages: string[] = [];
   clusterRef: ManagedObject;

   constructor(private vsanConfigService: VsanConfigService,
         private diskManagementService: DiskManagementService,
         private diskgroupService: DiskGroupMutationService,
         private changeDetector: ChangeDetectorRef) {
   }

   @Loader()
   async ngOnInit(): Promise<void> {
      const context: any = VsanUiUtils.getModalContext();
      // clusterRef is set via context when this dialog is opened from Datastore -> Capacity page.
      this.clusterRef = context?.clusterRef || ManagedObject.contextObject;
      this.vsanConfigSpec = await this.vsanConfigService.getVsanConfigSpec(this.clusterRef);
      // We need to await the vsan config spec before we can query the disks data
      this.hostsClaimingData = await this.queryHostClaimingData(this.vsanConfigSpec.isVsanMaxEnabled);
   }

   private async queryHostClaimingData(isVsanMaxEnabled: boolean): Promise<Map<string, VsanClaimDisksData>> {
      const hostsClaimingData: any = await this.diskManagementService.getHostsClaimingData(this.clusterRef,
            isVsanMaxEnabled);
      return new Map<string, VsanClaimDisksData>(Object.entries(hostsClaimingData));
   }

   @Loader({name: LoaderName.ON_CONFIRM})
   async onOkClicked(): Promise<void> {
      if (!this.isConfigValid) {
         return;
      }
      this.alertMessages = [];
      const tasks: ManagedObject[] = this.vsanConfigSpec.isVsanMaxEnabled ?
            [await this.claimVsanMaxDisks()] // Only one task from this API, so wrap as array here
            : await this.claimDiskGroups();

      VsanUiUtils.closeModalDialog(tasks);
   }

   private async claimDiskGroups(): Promise<ManagedObject[]> {
      const claimDisksTasks: Promise<ManagedObject[]>[] = this.claimDisksSpecs
            .filter(claimDisksSpec => !VsanUiUtils.isEmpty(claimDisksSpec.disks))
            .map(claimDisksSpec => this.diskgroupService.claimDiskGroup(claimDisksSpec.hostRef, claimDisksSpec));

      const tasksPerHost: ManagedObject[][] = await Promise.all(claimDisksTasks);
      return ArrayUtils.flatten(tasksPerHost);
   }

   private async claimVsanMaxDisks(): Promise<ManagedObject> {
      return this.diskgroupService.claimStoragePoolDisks(this.clusterRef, this.claimDisksSpecs);
   }

   @ErrorHandler()
   private handleError(error: string): void {
      Logger.debug("Failed to start one or more tasks!", error);
      this.alertMessages.push(error);
   }

   configAcceptabilityChanged(isValid: boolean): void {
      this.isConfigValid = isValid;
      this.changeDetector.detectChanges();
   }
}
