import {AfterViewInit, Component, EventEmitter, Input, Output, ViewChild} from "@angular/core";
import {HostData} from "@generated/host-data";
import {VsanUiUtils} from "@util/vsan-util";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {DiskManagementActionAvailabilityService} from "./validation/disk-management-action-availability.service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {Icon} from "@util/icon";
import {DiskGroupKey, DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {ClrDatagrid, ClrDatagridComparatorInterface} from "@clr/angular";
import {DiskMgmtExportConfig} from "./export/disk-mgmt-export-config";
import {ExportDataSource} from "@component/export/export-data-source";

@Component({
   templateUrl: "host-list.html",
   selector: "vsan-host-list",
   styleUrls: ["host-list.scss"],
})
export class HostListComponent implements AfterViewInit {

   @ViewChild("dataGrid")
   datagrid: ClrDatagrid;

   _hostList: HostData[];
   @Input()
   get hostList(): HostData[] {
      return this._hostList;
   }

   set hostList(value: HostData[]) {
      if (VsanUiUtils.isUnset(value)) {
         return;
      }
      this._hostList = value;
      value.forEach(host => {
         this.healthKeys.add(this.getHealthKey(host));
         this.stateKeys.add(this.getState(host));
         host.diskGroups.map(DiskMgmtDiskGroupUtil.getKey)
               .filter(group => !!group)
               .forEach(group => this.groupTypeKeys.add(group));
      });
   }

   Icon: typeof Icon = Icon;
   DiskMgmtExportConfig: typeof DiskMgmtExportConfig = DiskMgmtExportConfig;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   DiskManagementActionAvailabilityService: typeof DiskManagementActionAvailabilityService = DiskManagementActionAvailabilityService;
   hostGridPageSize: number = 15;

   @Input()
   autoClaimMode: boolean;

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   @Input()
   hasHostMaintenancePermission: boolean;

   @Input()
   selectedHost: HostData;

   @Output()
   hostSelected: EventEmitter<HostData> = new EventEmitter<HostData>();

   healthKeys: Set<string> = new Set<string>();
   stateKeys: Set<string> = new Set<string>();
   groupTypeKeys: Set<string> = new Set<string>();

   healthComparator: ClrDatagridComparatorInterface<HostData> = new HealthComparator();
   stateComparator: ClrDatagridComparatorInterface<HostData> = new StateComparator();

   exportDataSource: ExportDataSource;

   ngAfterViewInit(): void {
      this.exportDataSource = DiskMgmtExportConfig.createExportDataSource(this.datagrid);
   }

   hideDgPageSize(): boolean {
      return !this.hostList || this.hostList.length < this.hostGridPageSize;
   }

   getGroupTypeKeys = (host: HostData): string[] => {
      return DiskMgmtHostUtil.getGroupTypeKeys(host);
   };

   hideGroupTypeColumn = (): boolean => {
      return this.groupTypeKeys.size === 1 && this.groupTypeKeys.has(DiskGroupKey.VSAN);
   };

   hideFaultDomainColumn = (): boolean => {
      return this.hostList?.every(host => VsanUiUtils.isEmpty(host.faultDomain));
   };

   getState = (host: HostData): string => {
      return DiskMgmtHostUtil.getState(host);
   };

   getHealthKey = (host: HostData): string => {
      return DiskMgmtHostUtil.getHealthKey(host);
   };

   getClaimedDisksLabel = (host: HostData): string => {
      return DiskMgmtHostUtil.getClaimedDisksLabel(host);
   };

   selectionChanged(host: HostData): void {
      this.hostSelected.emit(host);
   }

   getHostSuffix(host: HostData): string {
      if (host.isWitnessHost && !host.isMetadataWitnessHost) {
         return VsanUiUtils.getString("vsan.host.list.witnessHost");
      } else if (host.isMetadataWitnessHost) {
         return VsanUiUtils.getString("vsan.host.list.metadataWitnessHost");
      }
      return "";
   }
}

export class HealthComparator implements ClrDatagridComparatorInterface<HostData> {
   compare(o1: HostData, o2: HostData): number {
      return DiskMgmtHostUtil.getHealthKey(o1)
            .localeCompare(DiskMgmtHostUtil.getHealthKey(o2));
   }
}

export class StateComparator implements ClrDatagridComparatorInterface<HostData> {
   compare(o1: HostData, o2: HostData): number {
      return DiskMgmtHostUtil.getState(o1)
            .localeCompare(DiskMgmtHostUtil.getState(o2));
   }
}
