import {VsanUiUtils} from "@util/vsan-util";
import {Injectable} from "@angular/core";
import {Icon} from "@util/icon";
import {HostData} from "@generated/host-data";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {AvailabilityState} from "@generated/availability-state";
import {DiskData} from "@generated/disk-data";
import {ClaimOption} from "@generated/claim-option";

/**
 * This service calculates counts for disk mgmt entities (i.e., hosts, disk groups, capacity disks,
 * vSAN direct disks, pmem storage) and provides a factory method for these entities on host and cluster level.
 */
@Injectable()
export class DiskMgmtEntitiesSummaryService  {

   private typeToModel: Map<DiskMgmtEntityType, DiskMgmtEntitySummary> = new Map([
      [DiskMgmtEntityType.HOST, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.HOST,
            Icon.HOST,
            "vsan.manage.diskManagement.overview.host",
            "vsan.manage.diskManagement.overview.hosts")],
      [DiskMgmtEntityType.CAPACITY_DISK, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.CAPACITY_DISK,
            Icon.CLAIM_AS_CAPACITY,
            "vsan.manage.diskManagement.overview.capacityDisk",
            "vsan.manage.diskManagement.overview.capacityDisks")],
      [DiskMgmtEntityType.DISK_GROUP, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.DISK_GROUP,
            Icon.DISK_GROUP_IMAGE,
            "vsan.manage.diskManagement.overview.diskGroup",
            "vsan.manage.diskManagement.overview.diskGroups")],
      [DiskMgmtEntityType.SINGLE_TIER_DISKS, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.SINGLE_TIER_DISKS,
            Icon.DISK,
            "vsan.manage.diskManagement.overview.singleTierDisk",
            "vsan.manage.diskManagement.overview.singleTierDisks")],
      [DiskMgmtEntityType.VSAN_DIRECT_DISK, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.VSAN_DIRECT_DISK,
            null,
            "vsan.manage.diskManagement.overview.vSanDirectDisk",
            "vsan.manage.diskManagement.overview.vSanDirectDisks")],
      [DiskMgmtEntityType.PMEM_HOST, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.PMEM_HOST,
            null,
            "vsan.manage.diskManagement.overview.pmemHost",
            "vsan.manage.diskManagement.overview.pmemHosts")],
      [DiskMgmtEntityType.PMEM_STORAGE, new DiskMgmtEntitySummary(
            DiskMgmtEntityType.PMEM_STORAGE,
            null,
            "vsan.manage.diskManagement.overview.pmemStorage",
            "vsan.manage.diskManagement.overview.pmemStorage")],
   ])  as Map<DiskMgmtEntityType, DiskMgmtEntitySummary>;

   private clusterLevelItems: DiskMgmtEntityType[] = [
      DiskMgmtEntityType.HOST,
      DiskMgmtEntityType.DISK_GROUP,
      DiskMgmtEntityType.CAPACITY_DISK,
      DiskMgmtEntityType.SINGLE_TIER_DISKS,
      DiskMgmtEntityType.VSAN_DIRECT_DISK,
      DiskMgmtEntityType.PMEM_HOST];

   private hostLevelItems: DiskMgmtEntityType[] = [
      DiskMgmtEntityType.DISK_GROUP,
      DiskMgmtEntityType.CAPACITY_DISK,
      DiskMgmtEntityType.SINGLE_TIER_DISKS,
      DiskMgmtEntityType.VSAN_DIRECT_DISK,
      DiskMgmtEntityType.PMEM_STORAGE];

   // Setters:
   private setHostCount(count: number): void {
      this.typeToModel.get(DiskMgmtEntityType.HOST).count = count;
   }

   private setDiskGroupCount(count: number): void {
      this.typeToModel.get(DiskMgmtEntityType.DISK_GROUP).count = count;
   }

   private setSingleTierDiskCount(count: number): void {
      this.typeToModel.get(DiskMgmtEntityType.SINGLE_TIER_DISKS).count = count;
   }

   private setCapacityDiskCount(count: number): void {
      this.typeToModel.get(DiskMgmtEntityType.CAPACITY_DISK).count = count;
   }

   private setVsanDirectDiskCount(count: number): void {
      this.typeToModel.get(DiskMgmtEntityType.VSAN_DIRECT_DISK).count = count;
   }

   private setPmemHostCount(count: number): void {
      this.typeToModel.get(DiskMgmtEntityType.PMEM_HOST).count = count;
      this.typeToModel.get(DiskMgmtEntityType.PMEM_STORAGE).count = count;
   }

   // Getters:
   private getHostCount(hostList: HostData[]): number {
      return hostList.length;
   }

   private getDiskGroupCount(hostList: HostData[]): number {
      return hostList.reduce((sum, host) =>
            sum += host.diskGroups.filter(DiskMgmtDiskGroupUtil.isVsanDiskGroup).length, 0);
   }

   private getSingleTierDiskCount(hostList: HostData[]): number {
      return hostList.reduce((sum, host) =>
            sum += host.diskGroups
                  .filter(DiskMgmtDiskGroupUtil.isVsanMaxSingleTier)
                  .reduce((diskSum, diskgroup) => diskSum += diskgroup.disks.length, 0),
            0);
   }

   private getCapacityDiskCount(hostList: HostData[]): number {
      return hostList.reduce((sum, host) => sum += (host.disks[AvailabilityState.IN_USE_BY_VSAN] as DiskData[])
            .filter(this.isClaimedForCapacity).length, 0);
   }
   private isClaimedForCapacity(disk: DiskData): boolean {
      return disk.claimOption === ClaimOption.ClaimForStorage;
   }

   private getVsanDirectDiskCount(hostList: HostData[]): number {
      return hostList.reduce((sum, host) => sum += host.disks[AvailabilityState.ONLY_MANAGED_BY_VSAN].length, 0);
   }

   private getPmemHostCount(hostList: HostData[]): number {
      return hostList.filter(host => host.pmemStorage[AvailabilityState.ONLY_MANAGED_BY_VSAN].length).length;
   }

   createEntities(isHostLevel: boolean, hostsData: HostData[]): DiskMgmtEntitySummary[] {
      this.populateSummary(hostsData);
      let summaries: DiskMgmtEntitySummary[] = this.getSummaries(isHostLevel);

      if (VsanUiUtils.isEmpty(summaries)) {
         summaries = [this.getEmptySummary(hostsData)];
      }

      return summaries;
   }

   private populateSummary(hostsData: HostData[]): void {
      this.setHostCount(this.getHostCount(hostsData));
      this.setDiskGroupCount(this.getDiskGroupCount(hostsData));
      this.setSingleTierDiskCount(this.getSingleTierDiskCount(hostsData));
      this.setCapacityDiskCount(this.getCapacityDiskCount(hostsData));
      this.setVsanDirectDiskCount(this.getVsanDirectDiskCount(hostsData));
      this.setPmemHostCount(this.getPmemHostCount(hostsData));
   }

   private getSummaries(isHostLevel: boolean): DiskMgmtEntitySummary[] {
      const result: DiskMgmtEntitySummary[] = [];
      const levelEntities: DiskMgmtEntityType[] = isHostLevel ? this.hostLevelItems : this.clusterLevelItems;
      levelEntities.forEach((itemType) => {
         const item: DiskMgmtEntitySummary = this.typeToModel.get(itemType);
         if (item.count) {
            result.push(this.typeToModel.get(itemType));
         }
      });

      return result;
   }

   // Handle empty summary:
   // * Host level - if host has no disks and groups show "0 vSAN disk groups" item.
   // * Cluster level - when there are no hosts, summary is not shown at all but unavailable-view component.
   private getEmptySummary(hostsData: HostData[]): DiskMgmtEntitySummary {
      const defaultType: DiskMgmtEntityType = hostsData.some(host => host.isSingleTierEnabled)
            ? DiskMgmtEntityType.SINGLE_TIER_DISKS : DiskMgmtEntityType.DISK_GROUP;
      return this.typeToModel.get(defaultType);
   }
}

export class DiskMgmtEntitySummary {
   constructor(public type: DiskMgmtEntityType,
         public icon: Icon,
         public labelKeySing: string,
         public labelKeyPlural: string,
         public count?: number) {}

   get label(): string {
      return VsanUiUtils.getString(this.count === 1 ? this.labelKeySing : this.labelKeyPlural, this.count);
   }
}

export enum DiskMgmtEntityType {
   HOST,
   CAPACITY_DISK,
   DISK_GROUP,
   SINGLE_TIER_DISKS,
   VSAN_DIRECT_DISK,
   PMEM_HOST,
   PMEM_STORAGE,
}
