import {AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild} from "@angular/core";
import {ClrDatagrid, ClrDatagridComparatorInterface, ClrDatagridPagination, ClrDatagridSortOrder} from "@clr/angular";
import {Icon} from "@util/icon";
import {DiskData} from "@generated/disk-data";
import {HostData} from "@generated/host-data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {ClaimableState} from "@util/disk-mgmt.util";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {DiskType} from "@generated/disk-type";
import {DiskStatus} from "@generated/disk-status";
import {ClaimOption} from "@generated/claim-option";
import {StorageIdentifier} from "@component/diskmgmt/model/storage-identifier";
import {StorageData} from "@component/diskmgmt/model/storage-data";
import {StorageGroupData} from "./model/storage-group-data";
import {DiskLocalityType} from "@generated/disk-locality-type";
import {DiskMgmtExportConfig} from "./export/disk-mgmt-export-config";
import {ExportDataSource} from "@component/export/export-data-source";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "storage-group-details.html",
   selector: "vsan-storage-details",
   styleUrls: ["storage-group-details.scss"],
   providers: [ReferenceWatcher],
})
export class StorageGroupDetailsComponent implements OnInit, AfterViewInit {

   @ViewChild("dataGrid")
   datagrid: ClrDatagrid;

   @Input()
   autoClaimMode: boolean;

   @Input()
   spaceEfficiencyConfig: SpaceEfficiencyConfig = SpaceEfficiencyConfig.NONE;

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   hasHostMaintenancePermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   private _storageGroup: StorageGroupData;

   @Input()
   set storageGroup(value: StorageGroupData) {
      this._storageGroup = value;
      if (!value) {
         this.storageItems = undefined;
         return;
      }
      this.storageItems = value.storageData;
      value.storageData.forEach(storage => {
         this.healthKeys.add(this.getDiskHealth(storage.diskStatus));
         this.diskTierKeys.add(this.getDiskTier(storage.claimOption));
         this.driveTypeKeys.add(this.getDriveType(storage.type));
         this.claimableStateKeys.add(this.getClaimableStateLabel(storage.claimableState));
         this.deviceMountStateKeys.add(this.getDiskMountedState(storage.diskStatus));
         this.deviceOperationalStateKeys.add(this.getDiskOperationalState(storage.deviceState));
         this.driveLocalityKeys.add(this.getDiskLocality(storage.driveLocality));
         this.transportTypeKeys.add(this.getTransportTypeLabel(storage.transportType));
         this.physicalLocationKeys.add(this.getPhysicalLocation(storage.physicalLocation));
      });
      this.updatePreselection();
   }
   get storageGroup(): StorageGroupData {
      return this._storageGroup;
   }

   @Input()
   host: HostData;

   @Input()
   preselectedStorage: StorageIdentifier;

   @Output()
   storageSelected: EventEmitter<StorageIdentifier> = new EventEmitter<StorageIdentifier>();

   healthKeys: Set<string> = new Set<string>();
   diskTierKeys: Set<string> = new Set<string>();
   driveTypeKeys: Set<string> = new Set<string>();
   claimableStateKeys: Set<string> = new Set<string>();
   deviceMountStateKeys: Set<string> = new Set<string>();
   deviceOperationalStateKeys: Set<string> = new Set<string>();
   driveLocalityKeys: Set<string> = new Set<string>();
   transportTypeKeys: Set<string> = new Set<string>();
   physicalLocationKeys: Set<string> = new Set<string>();

   healthComparator: ClrDatagridComparatorInterface<StorageData> = new HealthComparator();
   deviceMountStateComparator: ClrDatagridComparatorInterface<StorageData> = new DeviceMountStateComparator();
   deviceOperationalStateComparator: ClrDatagridComparatorInterface<StorageData> = new DeviceOperationalStateComparator();
   driveTypeComparator: ClrDatagridComparatorInterface<StorageData> = new DriveTypeComparator();
   claimableStateComparator: ClrDatagridComparatorInterface<StorageData> = new ClaimableStateComparator();
   diskTierComparator: ClrDatagridComparatorInterface<StorageData> = new DiskTierComparator();
   driveLocalityComparator: ClrDatagridComparatorInterface<StorageData> = new DriveLocalityComparator();
   transportTypeComparator: ClrDatagridComparatorInterface<StorageData> = new TransportTypeComparator();
   physicalLocationComparator: ClrDatagridComparatorInterface<StorageData> = new PhysicalLocationComparator();

   ClaimableState: typeof ClaimableState = ClaimableState;
   Icon: typeof Icon = Icon;
   DiskMgmtExportConfig: typeof DiskMgmtExportConfig = DiskMgmtExportConfig;
   storageItems: StorageData[] = [];
   selectedStorageData: StorageData;
   showGrid: boolean = false;

   ascSort: ClrDatagridSortOrder = ClrDatagridSortOrder.ASC;

   exportDataSource: ExportDataSource;
   readonly DEFAULT_ITEMS_PER_PAGE: number = 10;

   constructor(private refWatcher: ReferenceWatcher) { }

   ngOnInit(): void {
      this.updatePreselection();
   }

   /* The width of datagrid columns is not set when datagrid is inside an accordion.
      See https://github.com/vmware/clarity/issues/3696 This causes:
         1) Wrong column width when it is predefined.
         2) When copyToClipboard icon is shown the row flickers because of cells misalignment.
      The workaround is to display the grid on AfterViewInit.
    */
   ngAfterViewInit(): void {
      this.showGrid = true;
      this.refWatcher.setTimeout(() => this.exportDataSource = DiskMgmtExportConfig.createExportDataSource(this.datagrid), 0);
   }

   private updatePreselection(): void {
      this.selectedStorageData = !this.preselectedStorage ?
            this.sortDisksByHealth()[0] :
            this.storageItems.find(storage => StorageIdentifier.areEqual(this.preselectedStorage, storage));
      /* TODO: (vasilevd):
       * 1) Check why component is initialized twice.
       * 2) Default zero item selection is not passed to DiskManagementComponent, so that screen reader message
       * doesn't include the selected storage. Check StorageGroupsComponent.toggleExpand().
       */
      this.selectionChanged(this.selectedStorageData, false);
   }

   selectionChanged(storageData: StorageData, emitSelection: boolean = true): void {
      if (storageData && emitSelection) {
         this.storageSelected.emit(new StorageIdentifier(storageData.uuid, storageData.name));
      }
   }

   get claimableState(): ClaimableState {
      return this.selectedStorageData && this.selectedStorageData.claimableState;
   }

   getPhysicalLocation = (physicalLocation: string[]): string => {
      return DiskMgmtDiskUtil.getPhysicalLocation(physicalLocation);
   };

   getFsVersion = (diskStatus: DiskStatus): string => {
      return DiskMgmtDiskUtil.getFormatVersion(diskStatus);
   };

   getDiskHealth = (diskStatus: DiskStatus): string => {
      return DiskMgmtDiskUtil.getHealth(diskStatus);
   };

   getDriveType(type: DiskType): string {
      return DiskMgmtDiskUtil.getDiskType(type);
   }

   getDiskTier(claimOption: ClaimOption): string {
      return DiskMgmtDiskUtil.getLocalizedStorage(claimOption);
   }

   getDiskMountedState(diskStatus: DiskStatus): string {
      return DiskMgmtDiskUtil.localizeMountedState(diskStatus);
   }

   getDiskOperationalState(deviceState: DiskData.DeviceState): string {
      return DiskMgmtDiskUtil.localizeOperationalState(deviceState);
   }

   getDiskLocality(diskLocality: DiskLocalityType): string {
      return DiskMgmtDiskUtil.localizeLocality(diskLocality);
   }

   getClaimableStateLabel(claimableState: ClaimableState): string {
      return DiskMgmtDiskUtil.localizeClaimableState(claimableState);
   }

   getTransportTypeLabel(value: DiskData.StorageDeviceTransport): string {
      return DiskMgmtDiskUtil.getTransportTypeText(value);
   }

   get isVsanDiskGroup(): boolean {
      return DiskMgmtDiskGroupUtil.isVsanDiskGroup(this.storageGroup);
   }

   get isVsanMaxSingleTier(): boolean {
      return DiskMgmtDiskGroupUtil.isVsanMaxSingleTier(this.storageGroup);
   }

   get isNotPmemGroup(): boolean {
      return !DiskMgmtDiskGroupUtil.isPmemDiskGroup(this.storageGroup);
   }

   get isUnusedDiskGroup(): boolean {
      return DiskMgmtDiskGroupUtil.isUnusedDiskGroup(this.storageGroup);
   }

   getPaginationLabel(pagination: ClrDatagridPagination): string {
      return VsanUiUtils.getDatagridFooterLabel(this.storageItems, pagination, this.DEFAULT_ITEMS_PER_PAGE);
   }

   // Sort disks by health status in order to display unhealthy disks first
   sortDisksByHealth(): StorageData[] {
      return this.storageItems?.sort(DiskMgmtDiskUtil.compareDiskHealth);
   }
}

export class HealthComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.compareDiskHealth(o1, o2);
   }
}

export class DeviceMountStateComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.localizeMountedState(o1.diskStatus)
            .localeCompare(DiskMgmtDiskUtil.localizeMountedState(o1.diskStatus));
   }
}

export class DeviceOperationalStateComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.localizeOperationalState(o1.deviceState)
            .localeCompare(DiskMgmtDiskUtil.localizeOperationalState(o2.deviceState));
   }
}

export class DriveTypeComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.getDiskType(o1.type)
            .localeCompare(DiskMgmtDiskUtil.getDiskType(o2.type));
   }
}

export class ClaimableStateComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.localizeClaimableState(o1.claimableState)
            .localeCompare(DiskMgmtDiskUtil.localizeClaimableState(o2.claimableState));
   }
}

export class DiskTierComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.getLocalizedStorage(o1.claimOption)
            .localeCompare(DiskMgmtDiskUtil.getLocalizedStorage(o2.claimOption));
   }
}

export class DriveLocalityComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.localizeLocality(o1.driveLocality)
            .localeCompare(DiskMgmtDiskUtil.localizeLocality(o2.driveLocality));
   }
}

export class TransportTypeComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.getTransportTypeText(o1.transportType)
            .localeCompare(DiskMgmtDiskUtil.getTransportTypeText(o2.transportType));
   }
}

export class PhysicalLocationComparator implements ClrDatagridComparatorInterface<StorageData> {
   compare(o1: StorageData, o2: StorageData): number {
      return DiskMgmtDiskUtil.getPhysicalLocation(o1.physicalLocation)
            .localeCompare(DiskMgmtDiskUtil.getPhysicalLocation(o2.physicalLocation));
   }
}
