import {ClrLoadingState} from "@clr/angular";

/**
 * Used to pass @Input() property between components which knows about its own loading state.
 */
export class LoadingAwareProperty<PropertyType> {
   constructor(public data: PropertyType, private loadingState: ClrLoadingState = ClrLoadingState.DEFAULT) {}

   get isLoading(): boolean {
      return this.loadingState === ClrLoadingState.LOADING;
   }

   setToLoadingState(): void {
      this.loadingState = ClrLoadingState.LOADING;
   }

   setToDefaultState(): void {
      this.loadingState = ClrLoadingState.DEFAULT;
   }
}