import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "mark-ssd-prompt.html",
   styleUrls: ["mark-ssd-prompt.scss"],
   selector: "vsan-diskmgmt-mark-ssd",
})
export class MarkSsdComponent implements OnInit{

   isSsd: boolean;
   title: string;

   ngOnInit(): void {
      this.isSsd = VsanUiUtils.getModalContext();
      this.title = VsanUiUtils.getString(this.isSsd
            ? "storage.adapters.devices.action.tagAsSsd"
            : "storage.adapters.devices.action.untagAsSsd");
   }

}