import {Component} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {ConfigureVsanClusterMutationProvider} from "@generated/configure-vsan-cluster-mutation-provider";

@Component({
   templateUrl: "stop-managing-pmem.html",
   styleUrls: ["../actions-common.scss"],
   selector: "vsan-stop-managing-pmem",
})
export class StopManagingPmemComponent {
   alerts: string[];
   warningMessage: string;
   footerText: string;

   constructor(private configureClusterMutationProvider: ConfigureVsanClusterMutationProvider) { }

   ngOnInit(): void {
      this.warningMessage = VsanUiUtils.getString("vsan.dialog.stopManagingPmem.description");
      this.footerText = VsanUiUtils.getString("vsan.dialog.stopManagingPmem.confirm");
   }

   onOkClicked = () => {
      this.configureClusterMutationProvider.stopManagingPmem(ManagedObject.contextObject)
            .then(this.handleTask)
            .catch(this.onError);
   };

   private handleTask = (task: ManagedObject) => {
      VsanUiUtils.closeModalDialog(task);
   };

   private onError = (message: any) => {
      this.alerts = [message];
   };
}