import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {DiskData} from "@generated/disk-data";
import {DiskGroupData} from "@generated/disk-group-data";
import {ScsiDisk} from "@component/diskmgmt/model/scsi-disk.data";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";

@Component({
   templateUrl: "add-disk.html",
   styleUrls: ["../actions-common.scss"],
   selector: "vsan-diskmgmt-add-disk",
})
export class AddDiskComponent implements OnInit {

   @LoadingIndicator()
   busy: boolean;

   disksLoading: boolean = true;

   clusterRef: ManagedObject;
   diskGroup: DiskGroupData;
   selectedDisks: DiskData[] = [];
   alerts: string[];
   isVsanMaxEnabled: boolean;
   isAllFlashDiskGroup: boolean;

   constructor(private diskGroupMutationService: DiskGroupMutationService) {
   }

   @Loader({srMessage: "vsan.dialog.addDisksToDiskGroup.title", srMessageType: SrMessageType.PAGE_TITLE})
   ngOnInit(): void {
      this.clusterRef = ManagedObject.contextObject;
      this.diskGroup = VsanUiUtils.getModalContext();
      this.isVsanMaxEnabled = DiskMgmtDiskGroupUtil.isVsanMaxSingleTier(this.diskGroup);
      this.isAllFlashDiskGroup = DiskMgmtDiskGroupUtil.isAllFlashDiskGroup(this.diskGroup);
   }

   @ErrorHandler()
   private handleError(error: string): void {
      this.alerts = [error];
   }

   onLoadingComplete(): void {
      this.disksLoading = false;
   }

   // invoked when the primary button is clicked
   @Loader()
   async onOkClicked(): Promise<void> {
      if (this.selectedDisks.length === 0) {
         this.alerts = [this.isVsanMaxEnabled
               ? VsanUiUtils.getString("vsan.dialog.addDisksToStoragePool.error.selectDisks")
               : VsanUiUtils.getString("vsan.dialog.addDisksToDiskGroup.error.selectDisks")];
         return;
      }

      const taskRef: ManagedObject = this.isVsanMaxEnabled
            ? await this.addDisksForVsanMax()
            : await this.addDisksToDiskGroup();
      VsanUiUtils.closeModalDialog(taskRef);
   }

   private addDisksForVsanMax(): Promise<ManagedObject> {
      const diskNames: string[] = this.selectedDisks.map(selectedDisk => selectedDisk.disk.canonicalName);
      return this.diskGroupMutationService.addDisksToStoragePool(this.diskGroup.ownerHostRef, diskNames);
   }

   private addDisksToDiskGroup(): Promise<ManagedObject> {
      const disks: ScsiDisk[] = this.selectedDisks.map(selectedDisk => selectedDisk.disk);

      // find the SSD disk of the group we are adding to
      const ssd: DiskData = this.diskGroup.disks.find(d => d.isMappedAsCache);
      const diskMapping: VsanDiskMapping = VsanDiskMapping.Factory.create(ssd.disk, disks);

      return this.diskGroupMutationService.addDiskToDiskGroup(
            this.clusterRef,
            this.diskGroup.ownerHostRef,
            diskMapping);
   }

   getDialogTitle(): string {
      return this.isVsanMaxEnabled
            ? VsanUiUtils.getString("vsan.dialog.addDisksToStoragePool.title")
            : VsanUiUtils.getString("vsan.dialog.addDisksToDiskGroup.title");
   }
}
