import {VsanUiUtils} from "@util/vsan-util";
import {Injectable} from "@angular/core";
import {HostData} from "@generated/host-data";
import {DiskMgmtDiskGroupUtil, VsanDiskGroupLabelFormat} from "@util/disk-mgmt-disk-group.util";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {StorageIdentifier} from "@component/diskmgmt/model/storage-identifier";
import {StorageGroupIdentifier} from "@component/diskmgmt/model/storage-group-identifier";

/**
 * This service handles screen reader messages for disk management view.
 */
@Injectable()
export class DiskManagementScreenReaderService {

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   informPageLoad(preselectedHost: HostData, preselectedGroup: StorageGroupIdentifier,
         preselectedStorage: StorageIdentifier, isHostDetailsLevel: boolean, isVirtualObjectsView: boolean): void {
      if (!preselectedHost) {
         if (isVirtualObjectsView) {
            // Disk Management page was loaded, virtual objects for cluster are loading.
            this.srOnlyMessage = VsanUiUtils.getString("vsan.manage.diskManagement.srMessage.virtualObjects.cluster");
         } else {
            // Disk management page was loaded
            this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
                  VsanUiUtils.getString("manage.vsan.diskManagement"));
         }
         return;
      }
      const hostName: string = preselectedHost.name;
      if (!isHostDetailsLevel && !isVirtualObjectsView) {
         // Disk management page was loaded and {hostName} was selected.
         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded.andItemSelected",
               VsanUiUtils.getString("manage.vsan.diskManagement"), hostName);
         return;
      }
      if (!preselectedGroup) {
         if (isVirtualObjectsView) {
            // Disk Management page was loaded, virtual objects for host {hostName} are loading.
            this.srOnlyMessage =
                  VsanUiUtils.getString("vsan.manage.diskManagement.srMessage.virtualObjects.host", hostName);
         } else {
            // Disk Management page was loaded, disk groups for host {hostName} are listed.
            this.srOnlyMessage =
                  VsanUiUtils.getString("vsan.manage.diskManagement.srMessage.hostLevel.groups", hostName);
         }
         return;
      }
      const groupName: string = DiskMgmtDiskGroupUtil.getLabel(preselectedGroup, VsanDiskGroupLabelFormat.TYPE_AND_UUID);
      if (!preselectedStorage) {
         if (isVirtualObjectsView) {
            // Disk Management page was loaded, virtual objects for {groupName} from host {hostName} are loading.
            this.srOnlyMessage = VsanUiUtils.getString("vsan.manage.diskManagement.srMessage.virtualObjects.group",
                  groupName, hostName);
         } else {
            // Disk Management page was loaded and {groupName} was expanded.
            this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded.andItemExpanded",
                  VsanUiUtils.getString("manage.vsan.diskManagement"), groupName);
         }
      } else {
         if (isVirtualObjectsView) {
            // Disk Management page was loaded, virtual objects for {storageName} in {groupName} from
            // host {hostName} are loading.
            this.srOnlyMessage = VsanUiUtils.getString("vsan.manage.diskManagement.srMessage.virtualObjects.storage",
                  preselectedStorage.name, groupName, hostName);
         } else {
            // Disk Management page was loaded, {groupName} was expanded and {storageName} was selected.
            this.srOnlyMessage = VsanUiUtils.getString(
                  "vsan.manage.diskManagement.srMessage.hostLevel.groups.disk.singular",
                  groupName, preselectedStorage.name);
         }
      }
   }

   informVirtualObjectsLoad(): void {
      // Virtual objects loaded.
      this.srOnlyMessage = VsanUiUtils.getString("vsan.manage.diskManagement.srMessage.virtualObjects.loaded");
   }
}
