import {Component, OnInit} from "@angular/core";
import {MountDiskContext} from "@navigation/context/mount-disk-context";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {ErrorHandler} from "@service/flow/error-handler.decorator";

/**
 * This component is used to mount a Vsan Max disk.
 */
@Component({
   templateUrl: "mount-storage-pool-disk.html",
   selector: "vsan-mount-storage-pool-disk",
   styleUrls: ["mount-storage-pool-disk.scss", "../actions-common.scss"],
})
export class MountStoragePoolDiskComponent implements OnInit {

   @LoadingIndicator()
   loading: boolean;

   alerts: string[] = [];
   hostRef: ManagedObject;
   diskName: string;
   diskCanonicalName: string;

   constructor(private diskGroupMutationService: DiskGroupMutationService) {
   }

   ngOnInit(): void {
      const context: MountDiskContext = VsanUiUtils.getModalContext();
      this.hostRef = context.hostRef;
      this.diskName = context.diskName;
      this.diskCanonicalName = context.diskCanonicalName;
   }

   @Loader()
   async ok(): Promise<void> {
      this.alerts = [];

      // For mounting a disk to a storage pool, vsanAddStoragePoolDisk API is used
      const task: ManagedObject = await this.diskGroupMutationService.addDisksToStoragePool(
            this.hostRef, [this.diskCanonicalName]);
      VsanUiUtils.closeModalDialog(task);
   }

   @ErrorHandler()
   private handleError(error: any): void {
      this.alerts = [error];
   }
}
