import {Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {HostData} from "@generated/host-data";
import {DiskGroupData} from "@generated/disk-group-data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {DiskMgmtDiskGroupUtil, VsanDiskGroupLabelFormat} from "@util/disk-mgmt-disk-group.util";
import {StorageGroupData} from "./model/storage-group-data";
import {AccordionPanelToggleExpandEventArg} from "@directive/clr-accordion/clr-accordion-panel.directive";
import {StorageIdentifier} from "@component/diskmgmt/model/storage-identifier";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {StorageGroupIdentifier} from "@component/diskmgmt/model/storage-group-identifier";

@Component({
   templateUrl: "storage-groups.html",
   selector: "vsan-storage-groups",
   styleUrls: ["storage-groups.scss"],
})
export class StorageGroupsComponent {

   static GROUP_ID_PREFIX: string = "group-";
   static CLR_DROPDOWN_TRIGGER_ATTR: string = "clrdropdowntrigger";
   static ACTIONS_CLASS_HIDDEN: string = "is-hidden";
   static ACTIONS_CLASS_NONE: string = "is-none-display";
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   Icon: typeof Icon = Icon;

   @Input()
   autoClaimMode: boolean;

   @Input()
   spaceEfficiencyConfig: SpaceEfficiencyConfig = SpaceEfficiencyConfig.NONE;

   _host: HostData;
   @Input()
   get host(): HostData {
      return this._host;
   }

   set host(value: HostData) {
      this._host = value;
      if (VsanUiUtils.isUnset(value)) {
         this.storageGroups = [];
         return;
      }
      const storageGroups: StorageGroupData[] = [];
      if (!VsanUiUtils.isEmpty(value.diskGroups)) {
         storageGroups.push(...value.diskGroups.map(StorageGroupData.fromDiskGroup));
         const unusedStorageGroup: StorageGroupData = storageGroups.find(DiskMgmtDiskGroupUtil.isUnusedDiskGroup);
         StorageGroupData.setUnusedStorageData(value, unusedStorageGroup);
      }
      this.storageGroups = storageGroups;
   }

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   @Input()
   hasHostMaintenancePermission: boolean;

   @Input()
   preselectedGroupIdentifier: StorageGroupIdentifier;

   @Input()
   preselectedStorage: StorageIdentifier;

   @Output()
   groupSelected: EventEmitter<StorageGroupIdentifier> = new EventEmitter<StorageGroupIdentifier>();

   @Output()
   storageSelected: EventEmitter<StorageIdentifier> = new EventEmitter<StorageIdentifier>();

   openedGroupActionsDropdownEl: HTMLElement;
   storageGroups: StorageGroupData[];

   isUnusedDiskGroup(diskGroup: DiskGroupData): boolean {
      return DiskMgmtDiskGroupUtil.isUnusedDiskGroup(diskGroup);
   }

   getActionColumnClass(diskGroup: DiskGroupData): string {
      if (this.host?.isSingleTierEnabled) {
         return StorageGroupsComponent.ACTIONS_CLASS_NONE;
      }

      return this.isUnusedDiskGroup(diskGroup) ? StorageGroupsComponent.ACTIONS_CLASS_HIDDEN : "";
   }

   isGroupExpanded(groupData: StorageGroupData): boolean {
      return !!(this.preselectedGroupIdentifier &&
            (this.getDiskGroupLabel(this.preselectedGroupIdentifier, VsanDiskGroupLabelFormat.UUID) ===
             this.getDiskGroupLabel(groupData, VsanDiskGroupLabelFormat.UUID)));
   }

   createGroupId(groupData: StorageGroupData): string {
      const groupLabelFormatted: string = this.getDiskGroupLabel(groupData, VsanDiskGroupLabelFormat.TYPE_AND_UUID).toLowerCase()
            .replace(/\s/g, "-");
      return `${StorageGroupsComponent.GROUP_ID_PREFIX}${groupLabelFormatted}`;
   }

   /**
    *  Select lastly expanded/collapsed group. If a group is collapsed group selection is removed.
    */
   toggleExpand(args: AccordionPanelToggleExpandEventArg<StorageGroupData>): void {
      this.selectStorage(null);
      const storageGroupIdentifier: StorageGroupIdentifier =
            !args.isExpanded ? null : StorageGroupIdentifier.fromDiskGroup(args.model);
      this.groupSelected.emit(storageGroupIdentifier);
   }

   selectStorage(storageIdentifier: StorageIdentifier): void {
      this.storageSelected.emit(storageIdentifier);
   }

   getDiskGroupLabel = (group: DiskGroupData | StorageGroupIdentifier,
         labelFormat: VsanDiskGroupLabelFormat = VsanDiskGroupLabelFormat.TYPE): string => {
      return DiskMgmtDiskGroupUtil.getLabel(group, labelFormat);
   };

   getHealthKey = (group: DiskGroupData): string => {
      return DiskMgmtDiskGroupUtil.getHealthKey(group);
   };

   getState = (group: DiskGroupData): string => {
      return DiskMgmtDiskGroupUtil.getState(group);
   };

   getClaimedDisksLabel = (group: DiskGroupData): string => {
      if (this.isUnusedDiskGroup(group)) {
         const disksCount: number = group.disks.length + group.pmemStorage.length;
         return VsanUiUtils.getString(disksCount === 1 ?
               "vsan.manage.diskManagement.host.diskGroups.disk.label" :
               "vsan.manage.diskManagement.host.diskGroups.disks.label",
               disksCount);
      }
      if (!DiskMgmtDiskGroupUtil.isPmemDiskGroup(group)) {
         const disksCount: number = group.disks.length;
         return VsanUiUtils.getString(disksCount === 1 ?
               "vsan.manage.diskManagement.host.diskGroups.disk.label" :
               "vsan.manage.diskManagement.host.diskGroups.disks.label",
               disksCount);
      }
      const devicesCount: number = group.pmemStorage.length;
      return VsanUiUtils.getString( devicesCount === 1 ?
            "vsan.manage.diskManagement.host.diskGroups.device.label" :
            "vsan.manage.diskManagement.host.diskGroups.devices.label",
            devicesCount);
   };

   getGroupType = (group: DiskGroupData): string => {
      return DiskMgmtDiskGroupUtil.getType(group);
   };

   getAggregatedFsVersion = (group: DiskGroupData): string => {
      const formatVersion: string = DiskMgmtDiskGroupUtil.getAggregatedFsVersion(group);
      return VsanUiUtils.isEmpty(formatVersion) ? formatVersion :
            VsanUiUtils.getString("vsan.manage.diskManagement.host.diskGroups.version", formatVersion);
   };

   isGroupActionsDropdownOpen(triggerEl: HTMLElement): boolean {
      return this.openedGroupActionsDropdownEl === triggerEl;
   }

   handleAccordionTitleClick(e: Event, triggerEl: HTMLElement): void {
      const targetEl: HTMLElement = e.target as HTMLElement;
      const isClickedOnGroupActionsDropdown: boolean = triggerEl.contains(targetEl);

      if (isClickedOnGroupActionsDropdown) {
         // We need to manually cancel the event because it will toggle the accordion
         this.cancelAccordionToggleEvent(e);
         this.toggleGroupActionsDropdown(this.hasClrDropdownTriggerAttr(targetEl), triggerEl);
      } else {
         // If we click somewhere else then close the opened dropdown and don't cancel the toggle event
         this.openedGroupActionsDropdownEl = null;
      }
   }

   /**
    * Cancel accordion expand/collapse event when clicked on the group actions dropdown trigger btn/icon.
    */
   private cancelAccordionToggleEvent(e: Event): void {
      e.cancelBubble = true;
      e.stopImmediatePropagation();
      e.stopPropagation();
   }

   private hasClrDropdownTriggerAttr(el: HTMLElement): boolean {
      return el.hasAttribute(StorageGroupsComponent.CLR_DROPDOWN_TRIGGER_ATTR) ||
            el.parentElement.hasAttribute(StorageGroupsComponent.CLR_DROPDOWN_TRIGGER_ATTR);
   }

   /**
    * - If we click on a new group actions dropdown then open it.
    * - If we click on the currently opened group actions dropdown then close it.
    * - If we click on a dropdown item (e.g. Remove) then close the currently opened group actions dropdown.
    */
   private toggleGroupActionsDropdown(isDropdownTriggerClick: boolean, clickedDropdownEl: HTMLElement): void {
      this.openedGroupActionsDropdownEl =
            (isDropdownTriggerClick && !this.isGroupActionsDropdownOpen(clickedDropdownEl))
                  ? clickedDropdownEl
                  : null;
   }
}