import {Component, Input} from "@angular/core";
import {Icon} from "@util/icon";
import {EnumeratedFilterSelectionResolver} from "@component/datagrid/filter/enumerated-filter.component";

@Component({
   templateUrl: "disk-mgmt-filter.html",
   selector: "vsan-disk-mgmt-filter",
   styleUrls: ["disk-mgmt-filter.scss"],
})
export class DiskMgmtFilterComponent<T> {

   Icon: typeof Icon = Icon;
   keys: string[];

   @Input()
   set items(value: Set<string>) {
      if (!value || !value.size) {
         return;
      }
      this.keys = Array.from([...value.keys()].sort((a, b) => a.localeCompare(b)));
   }

   readonly comparator: EnumeratedFilterSelectionResolver<T, string>;
}
