import {ChangeDetectorRef, Component, OnInit} from "@angular/core";
import {StorageData} from "@component/diskmgmt/model/storage-data";
import {StorageGroupIdentifier} from "@component/diskmgmt/model/storage-group-identifier";
import {StorageIdentifier} from "@component/diskmgmt/model/storage-identifier";
import {VsanBreadcrumbService} from "@component/navigation/vsan-breadcrumb.service";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {UpgradeData} from "@component/upgrade/upgrade.data";
import {UpgradeService} from "@component/upgrade/upgrade.service";
import {VirtualObjectsTableStateService} from "@component/virtual-objects-table/virtual-objects-table-state.service";
import {DiskGroupData} from "@generated/disk-group-data";
import {DiskManagementService} from "@generated/disk-management-service";
import {HostData} from "@generated/host-data";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {UpdateDuplicatePciIdDevicesService} from "@generated/update-duplicate-pci-id-devices-service";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectsService} from "@generated/virtual-objects-service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanConfigService} from "@generated/vsan-config-service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {LoadingAwareProperty} from "@model/loading-aware-property";
import {DiskManagementNavigationContext} from "@navigation/context/disk-management-navigation-context";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {TaskMonitorService} from "@service/task-monitor-service";
import {Cached, TrackBy} from "@util/cache/cached.decorator";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {Logger} from "@util/logger";
import {RaceConditionSafe} from "@util/promise/race-condition-safe.decorator";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";
import {DiskManagementRoutingService} from "./disk-management-routing.service";
import {DiskManagementScreenReaderService} from "./disk-management-screen-reader.service";
import {ViewObjectsContext} from "./views/model/view-objects-context";
import {DiskManagementActionAvailabilityService} from "./views/validation/disk-management-action-availability.service";

@Component({
   templateUrl: "disk-management.html",
   styleUrls: ["disk-management.scss"],
   selector: "vsan-disk-management",
   providers: [ReferenceWatcher],
})
export class DiskManagementComponent implements OnInit {
   @LoadingIndicator()
   loading: boolean;

   clusterRef: ManagedObject = ManagedObject.contextObject;
   clusterName: string;
   hostList: HostData[];
   upgradeData: UpgradeData;
   vsanConfig: VsanConfigSpec;
   alertMessages: string[] = [];
   hasEditPermission: boolean = false;
   clusterCapabilities: VsanCapabilityData;
   hasHostMaintenancePermission: boolean;
   noHostsView: UnavailableViewSpec;
   preselectedHost: HostData;
   preselectedGroupIdentifier: StorageGroupIdentifier;
   preselectedStorageIdentifier: StorageIdentifier;
   preselectedGroup: DiskGroupData;
   preselectedStorage: StorageData;
   isVirtualObjectsView: boolean;
   virtualObjects: VirtualObjectModel[];
   hostIndex: number = -1;
   private isInitLoad: boolean = true;
   isBreadcrumbNavigationEnabled: boolean = false;
   private preselectContext: DiskManagementNavigationContext;
   isVsanUpgradeVisible: boolean = false;
   hasDuplicatePciIdDevices: LoadingAwareProperty<boolean> = new LoadingAwareProperty(false);

   constructor(private diskMgmtService: DiskManagementService,
         private diskManagementRoutingService: DiskManagementRoutingService,
         private diskManagementActionAvailabilityService: DiskManagementActionAvailabilityService,
         private diskManagementScreenReaderService: DiskManagementScreenReaderService,
         private dataService: DataServiceClient,
         private permissionService: PermissionService,
         private capabilityProvider: VsanCapabilityProvider,
         private taskService: TaskMonitorService,
         private vsanConfigService: VsanConfigService,
         private virtualObjectsService: VirtualObjectsService,
         private cdRef: ChangeDetectorRef,
         private refWatcher: ReferenceWatcher,
         private upgradeService: UpgradeService,
         private virtualObjectsTableState: VirtualObjectsTableStateService,
         private updatePciIdService: UpdateDuplicatePciIdDevicesService) {
   }

   async ngOnInit(): Promise<void> {
      this.refWatcher.watchSubscriptionRef(this.diskManagementActionAvailabilityService.onTaskStarted
            .subscribe(result => this.refreshRequested(result)));
      this.refWatcher.watchSubscriptionRef(this.diskManagementActionAvailabilityService.onObjectsViewSelected
            .subscribe(result => this.viewObjects(result)));
      this.refWatcher.watchSubscriptionRef(this.diskManagementActionAvailabilityService.onHostDetailsViewSelected
            .subscribe(() => this.setSelectHostIndex()));
      this.refWatcher.watchSubscriptionRef(this.diskManagementRoutingService.onClusterNavigatedObs
            .subscribe(() => {
               this.selectClusterView();
               this.diskManagementRoutingService.resetRootCallBack();
            }));
      this.refWatcher.watchSubscriptionRef(this.diskManagementRoutingService.onHostNavigatedObs
            .subscribe(host => this.onHostNavigated(host)));
      this.refWatcher.watchSubscriptionRef(this.diskManagementRoutingService.onVirtualObjectsRequested
            .subscribe(viewObjectsContext => this.requestVirtualObjects(viewObjectsContext)));
      this.refWatcher.watchSubscriptionRef(this.diskManagementRoutingService.onErrorCleared
            .subscribe(() => this.clearErrors()));

      this.preselectContext = VsanUiUtils.getNavigationData();
      this.initializeBreadcrumbNavigation();
      this.capabilityProvider.getVcCapabilityData(this.clusterRef)
            .then((vcCapabilities: VsanCapabilityData) => {
               if (vcCapabilities.isDuplicatePciIdFixSupported) {
                  this.checkDuplicatePciIdDevices();
               }
            })
            .catch(error => Logger.warn("Unable to get vc capabilities: ", error));

      this.permissionService.hasPermissions(this.clusterRef, [Privileges.HOST_MAINTENANCE()])
            .then(result => this.hasHostMaintenancePermission = result)
            .catch(error => Logger.warn("Unable to check host maintenance privilege: ", error));

      this.permissionService.hasPermissions(this.clusterRef,
            [Privileges.EDIT_CLUSTER(), Privileges.CONFIG_STORAGE()])
            .then(result => this.hasEditPermission = result)
            .catch(error => Logger.warn("Unable to check cluster permissions: ", error));

      try {
         await this.fetch();
      } catch (err) {
         this.handleError(err);
      }
   }

   private checkDuplicatePciIdDevices(): void {
      this.hasDuplicatePciIdDevices.setToLoadingState();
      this.updatePciIdService.hasDuplicatePciIdDevices(this.clusterRef)
            .then(result => this.hasDuplicatePciIdDevices.data = result)
            .catch(error => Logger.warn("Unable to get duplicate PCI ID devices status: ", error))
            .finally(() => this.hasDuplicatePciIdDevices.setToDefaultState());
   }

   private initializeBreadcrumbNavigation(): void {
      // Initialize component routes
      this.diskManagementRoutingService.setInitialViewRoots();
      VsanBreadcrumbService.enable
            .subscribe(isEnabled => {
               this.isBreadcrumbNavigationEnabled = isEnabled;
               this.cdRef.detectChanges();
            });
   }

   onHostNavigated(host: HostData): void {
      this.selectHost(host);
      // Update the selected host index if the view is on host details level
      if (this.isHostDetailsLevel()) {
         this.setSelectHostIndex();
      }
   }

   @RefreshHandler
   @Loader()
   async fetch(): Promise<void> {

      // remember what is currently selected to preselect it after the reload
      this.prepareReloadContext();
      this.reset();

      let hostsData: HostData[];
      [this.vsanConfig, hostsData, this.clusterCapabilities, this.clusterName] = await Promise.all([
         this.vsanConfigService.getVsanConfigSpec(this.clusterRef),
         this.diskMgmtService.listHosts(this.clusterRef),
         // Get cluster level capabilities on each global refresh because we may add/remove old host in the meantime.
         this.capabilityProvider.getClusterCapabilityData(this.clusterRef),
         this.dataService.getProperty(ClusterComputeResource.name, this.clusterRef),
      ]);

      this.processHostsData(hostsData);
      if (this.isInitLoad) {
         this.diskManagementRoutingService.showBreadcrumbNavigation();
         this.isInitLoad = false;
      }
      // Load all of the fetched host names and pass them to the hosts level navigation
      this.diskManagementRoutingService.updateHostLinks();
      this.preselect();
      this.preselectVirtualObjects();
      this.diskManagementScreenReaderService.informPageLoad(
            this.preselectedHost,
            this.preselectedGroupIdentifier,
            this.preselectedStorageIdentifier,
            this.isHostDetailsLevel(),
            this.isVirtualObjectsView);
   }

   private reset(): void {
      this.setHostList(null);
      this.upgradeData = null;
      this.virtualObjects = null;
      this.preselectDiskMgmtEntities(null, null, null);
      this.selectClusterView();
      this.isVirtualObjectsView = false;
      this.clearErrors();
      this.noHostsView = null;
   }

   vsanUpgradeComplete(): void {
      this.isVsanUpgradeVisible = true;
      this.fetch();
   }

   private prepareReloadContext(): void {
      if (!this.preselectedHost && !this.isVirtualObjectsView) {
         // this is the first load of the current page, nothing to change
         return;
      }
      this.preselectContext = {
         hostRef: this.preselectedHost?.hostRef,
         diskGroup: this.preselectedGroupIdentifier,
         storage: this.preselectedStorageIdentifier,
         isVirtualObjectsView: this.isVirtualObjectsView,
         isHostDetailsLevel: this.isHostDetailsLevel() && !this.isVirtualObjectsView,
      };
   }

   private processHostsData(data: HostData[]): void {
      if (VsanUiUtils.isEmpty(data)) {
         this.noHostsView = UnavailableViewSpec.DISK_MGMT_VIEW_NO_HOSTS;
         return;
      }
      DiskMgmtHostUtil.orderHostsByHealth(data);
      this.setHostList(data);
      this.upgradeService.getUpgradeOnlyData(this.clusterRef, this.hostList)
            .then((result: UpgradeData) => {
               this.upgradeData = result;
            })
            .catch(this.handleError);
      this.hostList.forEach(host =>
            DiskMgmtUtil.sortDiskGroups(host.diskGroups,
                  (diskGroup) => DiskMgmtDiskGroupUtil.isVsanDiskGroup(diskGroup)));
   }

   private setHostList(data: HostData[]): void {
      this.hostList = data;
      this.diskManagementRoutingService.setHostList(this.hostList);
   }

   async fetchPrecheckUpgradeData(): Promise<void> {
      this.upgradeService.getUpgradeStatus(this.clusterRef)
            .then(upgradeStatus => {
               // Create a new instance to trigger the upgrade component's refresh
               this.upgradeData = this.upgradeData.getNewInstance(upgradeStatus);
            }).catch(this.handleError);
   }

   private preselect(): void {
      if (!this.preselectContext || !this.preselectContext.hostRef) {
         if (VsanUiUtils.isEmpty(this.hostList) ||
               (this.preselectContext && this.preselectContext.isVirtualObjectsView)) {
            return;
         }

         if (this.preselectContext && this.preselectContext.hostName) {
            // If only host name is passed they find the host by name
            this.preselectContext.hostRef =
                  this.hostList.find(host => host.name === this.preselectContext.hostName).hostRef;
         } else {
            // Select the first host if non virtual objects view and no host selection.
            this.preselectContext = {
               hostRef: this.hostList[0].hostRef,
            };
         }
      }
      this.preselectHost();
      if (this.preselectedHost && this.preselectContext.isHostDetailsLevel) {
         this.setSelectHostIndex();
      }
      this.preselectDiskGroup();
      this.preselectStorage();

   }

   private preselectVirtualObjects(): void {
      if (!this.preselectContext || !this.preselectContext.isVirtualObjectsView) {
         return;
      }
      this.viewObjects(new ViewObjectsContext(this.preselectedHost, this.preselectedGroup, this.preselectedStorage));
   }

   private preselectHost(): void {
      const selectedHost: HostData =
            this.hostList.find(host => host.hostRef.value === this.preselectContext.hostRef.value);
      if (selectedHost) {
         this.selectHost(selectedHost);
      }
   }

   private preselectDiskGroup(): void {
      if (!this.preselectedHost || (!this.preselectContext.diskGroup && !this.preselectContext.storage)) {
         return;
      }
      // Find group from the passed context
      if (this.preselectContext.diskGroup) {
         this.preselectedGroup = this.findSelectedGroupByIdentifier(this.preselectContext.diskGroup);
      }
      // If group from the context is not found, try to find the group of the passed storage.
      if (!this.preselectedGroup && this.preselectContext.storage) {
         this.preselectedGroup = DiskMgmtDiskGroupUtil.findGroupByStorage(this.preselectedHost.diskGroups,
               this.preselectContext.storage);
      }
      if (this.preselectedGroup) {
         this.preselectedGroupIdentifier = StorageGroupIdentifier.fromDiskGroup(this.preselectedGroup);
      }
   }

   private findSelectedGroupByIdentifier(groupIdentifier: StorageGroupIdentifier): DiskGroupData {
      if (!this.preselectedHost || VsanUiUtils.isUnset(groupIdentifier)) {
         return null;
      }
      return this.preselectedHost.diskGroups.find(diskGroup =>
            StorageGroupIdentifier.areEqual(diskGroup, groupIdentifier));
   }

   private preselectStorage(): void {
      if (!this.preselectedGroup || !this.preselectContext.storage) {
         return;
      }
      const isStorageIdentifierValid: boolean =
            !!DiskMgmtDiskGroupUtil.findGroupByStorage([this.preselectedGroup], this.preselectContext.storage);
      if (isStorageIdentifierValid) {
         this.selectStorage(this.preselectContext.storage);
      }
   }

   refreshRequested = (taskRefs?: ManagedObject[]): void => {
      if (!VsanUiUtils.isEmpty(taskRefs)) {
         Promise.all(taskRefs.map(task => this.taskService.getAwaitResult(task)))
               .then(r => this.fetch())
               .catch(error => {
                  const errorMessage: string = error.localizedMessage || error;
                  this.handleError(errorMessage);
               });
      } else {
         this.fetch();
      }
   };

   selectHost(host: HostData): void {
      if (!host) {
         return;
      }
      let group: StorageGroupIdentifier = null;
      // Preselect vSAN Max section if available
      if (host.isSingleTierEnabled && !VsanUiUtils.isEmpty(host.diskGroups)) {
         group = StorageGroupIdentifier.fromDiskGroup(host.diskGroups[0]);
      }

      this.preselectDiskMgmtEntities(host, group, null);
   }

   selectGroup(diskGroupIdentifier: StorageGroupIdentifier): void {
      this.preselectedGroupIdentifier = diskGroupIdentifier;
      this.preselectedGroup = this.findSelectedGroupByIdentifier(diskGroupIdentifier);
   }

   selectStorage(storageIdentifier: StorageIdentifier): void {
      this.preselectedStorageIdentifier = storageIdentifier;
      this.preselectedStorage = StorageData.findStorageByIdentifier(this.preselectedGroup, storageIdentifier);
   }

   isHostDetailsLevel(): boolean {
      return this.hostIndex > -1;
   }

   getExitVirtualObjectsLabel(): string {
      if (!this.isHostDetailsLevel()) {
         return VsanUiUtils.getString("vsan.manage.diskManagement.virtualObjects.back.hostList");
      }

      return this.vsanConfig.isVsanMaxEnabled
            ? VsanUiUtils.getString("vsan.manage.diskManagement.virtualObjects.back.disks")
            : VsanUiUtils.getString("vsan.manage.diskManagement.virtualObjects.back.disksAndGroups");
   }

   setSelectHostIndex(): void {
      this.hostIndex = this.hostList.findIndex(h => ManagedObject.areEqual(h.hostRef, this.preselectedHost.hostRef));
      if (this.isHostDetailsLevel()) {
         this.diskManagementRoutingService.navigateToHostRoute(this.preselectedHost.name);
         this.diskManagementRoutingService.setHostDetailsLevelRootCb();
      }
   }

   private selectClusterView(): void {
      this.hostIndex = -1;
   }

   @ErrorHandler()
   private handleError(error: string): void {
      this.alertMessages.push(error);
   }

   clearErrors(): void {
      this.alertMessages = [];
   }

   viewObjects(context: ViewObjectsContext): void {
      this.setIsVirtualObjectsView(true);
      this.diskManagementRoutingService.viewObjects(context);
   }

   private requestVirtualObjects(viewObjectsContext: ViewObjectsContext): void {
      // If we don't reset the VO table state we will keep all previous selections
      this.virtualObjectsTableState.reset();
      let objectUuids: string[] = null;
      // Preselect host, group and storage when selected from virtual objects,
      // in order to preserve them on refresh.
      if (!viewObjectsContext) {
         this.selectClusterView();
         this.preselectDiskMgmtEntities(null, null, null);
      } else if (viewObjectsContext.storage) {
         objectUuids = viewObjectsContext.storage.objectUuids;
         this.preselectDiskMgmtEntities(viewObjectsContext.host,
               StorageGroupIdentifier.fromDiskGroup(viewObjectsContext.group),
               new StorageIdentifier(viewObjectsContext.storage.uuid, viewObjectsContext.storage.name));
      } else if (viewObjectsContext.group) {
         objectUuids = viewObjectsContext.group.objectUuids;
         this.preselectDiskMgmtEntities(viewObjectsContext.host,
               StorageGroupIdentifier.fromDiskGroup(viewObjectsContext.group), null);
      } else if (viewObjectsContext.host) {
         objectUuids = viewObjectsContext.host.objectUuids;
         this.preselectDiskMgmtEntities(viewObjectsContext.host, null, null);
      }

      if (objectUuids === null || !VsanUiUtils.isEmpty(objectUuids)) {
         this.virtualObjects = null;
         this.queryVirtualObjects(objectUuids)
               .then((loadedObjects: VirtualObjectModel[]) => this.handleQueryVirtualObjectsResult(loadedObjects))
               .catch((reason: any) => this.handleQueryVirtualObjectsError(reason));
      } else {
         this.setVirtualObjects([]);
      }
   }

   /**
    * Query virtual objects for the passed objectUuids. If objectUuids is:
    * 1) Empty array - don't query virtual objects.
    * 2) Non-empty array - query virtual objects for the passed objectUuids.
    * 3) Null - query all virtual objects for the cluster.
    */
   @RaceConditionSafe()
   @Cached({trackBy: TrackBy.INPUT_VALUES})
   private async queryVirtualObjects(objectUuids: string[]): Promise<VirtualObjectModel[]> {
      return this.virtualObjectsService.listVirtualObjects(ManagedObject.contextObject, objectUuids);
   }

   private handleQueryVirtualObjectsResult(loadedObjects: VirtualObjectModel[]): void {
      // If virtualObjects is null that mean the we still wait for the objects and we display the loading spinner.
      // A case when it's not null is when we select an item without virtual objects on it.
      if (this.isVirtualObjectsView && this.virtualObjects === null) {
         this.setVirtualObjects(loadedObjects);
      }
   }

   private handleQueryVirtualObjectsError(reason: any): void {
      if (this.isVirtualObjectsView) {
         this.handleError(reason);
         this.setVirtualObjects([]);
      }
   }

   private setVirtualObjects(loadedVirtualObjects: VirtualObjectModel[]): void {
      this.virtualObjects = loadedVirtualObjects;
      this.diskManagementScreenReaderService.informVirtualObjectsLoad();
   }

   exitVirtualObjects(): void {
      this.setIsVirtualObjectsView(false);
      this.virtualObjects = null;
      this.diskManagementRoutingService.resetHostsRoutes();
      const hostToSelect: HostData = this.preselectedHost || this.hostList[0];
      this.onHostNavigated(hostToSelect);
      if (!this.isHostDetailsLevel()) {
         this.diskManagementRoutingService.resetRootCallBack();
         this.diskManagementRoutingService.navigateToRootRoute();
      }
   }

   private setIsVirtualObjectsView(value: boolean): void {
      this.isVirtualObjectsView = value;
      this.diskManagementRoutingService.setIsVirtualObjectsView(value);
   }

   private preselectDiskMgmtEntities(host: HostData, group: StorageGroupIdentifier, storage: StorageIdentifier): void {
      this.preselectedHost = host;
      this.selectGroup(group);
      this.selectStorage(storage);
   }
}
