import {Component} from "@angular/core";
import {ScsiDisk} from "@component/diskmgmt/model/scsi-disk.data";
import {DecommissionMode} from "@generated/decommission-mode";
import {DiskData} from "@generated/disk-data";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {RemoveDiskSpec} from "@generated/remove-disk-spec";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {DeleteVsanDirectDisksContext} from "@navigation/context/delete-vsan-direct-disks-context";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {VsanSignpostUtil} from "@util/vsan-signpost-util";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "delete-vsan-direct-disks.html",
   styleUrls: ["../actions-common.scss"],
   selector: "vsan-delete-vsan-direct-disks",
})
export class DeleteVsanDirectDisksComponent {
   private readonly SIGNPOST_CONTENT_WIDTH: number = 360;
   private readonly SIGNPOST_CONTENT_HEIGHT: number = 146;
   readonly dataMigrationModeSignpostId: string = "data-migration-mode-signpost";

   alerts: string[];
   disks: DiskData[];
   warningMessage: string;
   footerText: string;
   isDecomModeForVsanDirectDisksSupported: boolean;
   selectedDecommissionMode: DecommissionMode = DecommissionMode.noAction;
   unsupportedDecommissionModes: DecommissionMode[] = [DecommissionMode.ensureObjectAccessibility];

   @LoadingIndicator({
      name: [LoaderName.ON_INIT, LoaderName.ON_CONFIRM],
   })
   busy: boolean;

   constructor(private diskGroupMutationService: DiskGroupMutationService,
         private capabilitiesService: VsanCapabilityProvider) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.dialog.deleteVsanDirectDisk.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      const context: DeleteVsanDirectDisksContext = VsanUiUtils.getModalContext();
      this.disks = context.disks;
      this.warningMessage = VsanUiUtils.getString("vsan.dialog.deleteVsanDirectDisk.description");
      this.footerText = this.disks.length === 1
            ? VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.footerRemoveDisk")
            : VsanUiUtils.getString("vsan.confirmDiskRemoveOperation.footerRemoveDisks");

      this.isDecomModeForVsanDirectDisksSupported =
            await this.capabilitiesService.getIsDecomModeForVsanDirectDisksSupported(ManagedObject.contextObject);
      if (this.isDecomModeForVsanDirectDisksSupported) {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.DELETE_VSAN_DIRECT_DISKS_HEIGHT_PRECHECK);
      }
   }

   onSelectedDecommissionModeChanged = (decommissionMode: DecommissionMode) => {
      this.selectedDecommissionMode = decommissionMode;
   };

   @Loader({name: LoaderName.ON_CONFIRM})
   async onOkClicked(): Promise<void> {
      const scsiDisks: ScsiDisk[] = this.disks.map(d => d.disk);
      const spec: RemoveDiskSpec = RemoveDiskSpec.Factory.create(this.selectedDecommissionMode, scsiDisks);

      const task: ManagedObject = await this.diskGroupMutationService.removeVsanDirectDisks(ManagedObject.contextObject,
            spec);
      this.handleTask(task);
   }

   private handleTask = (task: ManagedObject) => {
      VsanUiUtils.closeModalDialog(task);
   };

   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private onDeleteOperationError(message: any): void {
      this.alerts = [message];
   }

   getDataMigrationModeSignpostPosition(): string {
      return VsanSignpostUtil.getPosition(this.dataMigrationModeSignpostId, this.SIGNPOST_CONTENT_WIDTH,
            this.SIGNPOST_CONTENT_HEIGHT, true);
   }
}
