import {DiskType} from "@generated/disk-type";
import {DiskData} from "@generated/disk-data";
import {DiskLocalityType} from "@generated/disk-locality-type";
import {ClaimOption} from "@generated/claim-option";
import {DiskStatus} from "@generated/disk-status";
import {PmemDiskData} from "@generated/pmem-disk-data";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {ClaimableState} from "@util/disk-mgmt.util";
import {StorageCapacity} from "@generated/storage-capacity";
import {DiskGroupData} from "@generated/disk-group-data";
import {StorageIdentifier} from "@component/diskmgmt/model/storage-identifier";

/**
 * ScsiDisk or PMemStorage presenter.
 */
export class StorageData {

   private constructor(
         public uuid: string,
         public name: string,
         public iconShape: string,
         public type: DiskType,
         public capacity: StorageCapacity,
         public deviceState: DiskData.DeviceState,
         public vsanUuid: string,
         public diskGroup: string,
         public driveLocality: DiskLocalityType,
         public physicalLocation: string[],
         public diskAdapter: string,
         public transportType: DiskData.StorageDeviceTransport,
         public claimOption: ClaimOption,
         public diskStatus: DiskStatus,
         public storage: DiskData | PmemDiskData,
         public claimableState: ClaimableState,
         public objectUuids: string[]) { }

   static fromDiskData(disk: DiskData, claimableState?: ClaimableState): StorageData {
      return new StorageData(
            disk.uuid,
            disk.name,
            DiskMgmtDiskUtil.getIcon(disk),
            DiskMgmtDiskUtil.getType(disk),
            disk.capacity,
            disk.deviceState,
            disk.vsanUuid,
            disk.diskGroup,
            disk.driveLocality,
            disk.physicalLocation,
            disk.diskAdapter,
            disk.transportType,
            disk.claimOption,
            disk.diskStatus,
            disk,
            claimableState,
            disk.objectUuids);
   }

   static fromPmemDiskData(storage: PmemDiskData, claimableState?: ClaimableState): StorageData {
      return new StorageData(
            storage.uuid,
            storage.name,
            "",
            DiskType.PMEM,
            storage.capacity,
            DiskMgmtDiskUtil.getPMemMountDiskState(storage.diskStatus),
            "",
            "",
            null,
            [],
            "",
            null,
            storage.claimOption,
            storage.diskStatus,
            storage,
            claimableState,
            storage.objectUuids);
   }

   static findStorageByIdentifier(group: DiskGroupData, storage: StorageIdentifier): StorageData {
      if (!group || !storage) {
         return null;
      }
      const diskData: DiskData = group.disks?.find(disk => StorageIdentifier.areEqual(disk, storage));
      if (diskData) {
         return StorageData.fromDiskData(diskData);
      }
      const device: PmemDiskData = group.pmemStorage?.find(disk => StorageIdentifier.areEqual(disk, storage));
      if (device) {
         return StorageData.fromPmemDiskData(device);
      }
      return null;
   }
}