import {Pipe, PipeTransform} from "@angular/core";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";

@Pipe({
   name: "hostHealthIconShape",
})
export class HostHealthIconShapePipe implements PipeTransform {

   transform(healthKey: string): string {
      switch (healthKey) {
         case DiskMgmtHostUtil.HEALTHY_MSG_KEY:
            return Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE;
         case DiskMgmtDiskGroupUtil.LOCKED_MSG_KEY:
            return Icon.CLR_WARN_STANDARD_ICON_SHAPE;
         case DiskMgmtHostUtil.UNHEALTHY_MSG_KEY:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
         case DiskMgmtHostUtil.UNKNOWN_MSG_KEY:
            return Icon.CLR_HELP;
         case DiskMgmtDiskGroupUtil.UNMOUNTED_MSG_KEY:
            return Icon.EMPTY_ICON;
         default:
            Logger.warn("Unknown health key: " + healthKey);
            return Icon.EMPTY_ICON;
      }
   }
}