import {Subject} from "rxjs";
import {BreadcrumbRouteModel} from "./breadcrumb-route.model";

export class VsanBreadcrumbService {
   /**
    * Keeps the routes keys of all components having @BreadcrumbNavigation annotated field in the correct order.
    * Example:
    * Parent component view routes:
    *     parentViewRoutes = {
    *        parentRoot: ...,
    *        parentLevelA: ...,
    *        parentLevelB: ...,
    *     }
    * Child component view routes:
    *     childViewRoutes = {
    *        childRoot: ...,
    *        childLevelA: ...,
    *        childLevelB: ...,
    *     }
    * Then the routeOrders will be [parentRoot, parentLevelA, parentLevelB, childRoot, childLevelA, childLevelB]
    * This information is needed in order to be known navigated routes order even in multiple components navigation.
    */
   private static routeOrders: string[] = [];

   static addRouteOrders(keys: string[]) {
      VsanBreadcrumbService.routeOrders.push(...keys);
   }

   static getRouteOrders(): string[] {
      return VsanBreadcrumbService.routeOrders;
   }

   /**
    * Triggers event when breadcrumb navigation is enabled.
    */
   static enable: Subject<boolean> = new Subject();

   /**
    * Triggered when navigation from a component occurs.
    */
   static route: Subject<BreadcrumbRouteModel> = new Subject();

   /**
    * Triggered when route link is clicked in the breadcrumb nav.
    */
   static onRouteClick: Subject<BreadcrumbRouteModel> = new Subject();

   static show(value: boolean) {
      VsanBreadcrumbService.enable.next(value);
   }
}

/** Example usage
 *  1. Define routing object and annotate it with @BreadcrumbNavigation.
 *     @BreadcrumbNavigation
 *     routes: any;
 *  2. Initialize the routing object in the ngOnInit hook.
 *     ngOnInit {
 *        this.routes = {
 *          root: BreadcrumbRouteModel.link(onRouteTriggeredCallbackName, "some link value"),
 *          levelA: BreadcrumbRouteModel.link(onRouteTriggeredCallbackName1, "some link value"),
 *          levelB: BreadcrumbRouteModel.link(onRouteTriggeredCallbackName2, "some link value"),
 *        };
 *     }
 *  3. Define the callback methods (onRouteTriggeredCallbackName).
 *     They will be called when the corresponding route link is clicked.
 *  4. Navigate to route from the component
 *     this.routes.levelA.navigate()
 *  NOTE: fields annotated with @BreadcrumbNavigation must be initialized in angular hooked callback like the ngOnInit.
 */
export function BreadcrumbNavigation(): any {
   return (target: any, propertyKey: string) => {
      let _routes: any = null;
      Object.defineProperty(target, propertyKey, {
         get: () => {
            return _routes;
         },
         set: function (routes: any) {
            // Gets reference to the angular component
            const componentContext = this;
            _routes = routes;
            const keys = Object.keys(routes);
            keys.forEach((key => {
               (routes[key] as BreadcrumbRouteModel).key = key;
            }));
            // Pass the newly initialized component routes keys
            VsanBreadcrumbService.addRouteOrders(keys);
            VsanBreadcrumbService.onRouteClick.subscribe(route => {
               if (!_routes[route.key]) {
                  return;
               }
               _routes[route.key] = route;
               // Call the angular component callback by given route
               if (componentContext[_routes[route.key].onTrigger]) {
                  componentContext[_routes[route.key].onTrigger]();
               }
            });
         },
      });
   };
}
