import {VsanUiUtils} from "@util/vsan-util";
import {Injectable} from "@angular/core";
import {Observable, Subject} from "rxjs";
import {BreadcrumbNavigation, VsanBreadcrumbService} from "@component/navigation/vsan-breadcrumb.service";
import {BreadcrumbRouteModel} from "@component/navigation/breadcrumb-route.model";
import {HostData} from "@generated/host-data";
import {DiskGroupData} from "@generated/disk-group-data";
import {DiskMgmtDiskGroupUtil, VsanDiskGroupLabelFormat} from "@util/disk-mgmt-disk-group.util";
import {DiskData} from "@generated/disk-data";
import {PmemDiskData} from "@generated/pmem-disk-data";
import {ViewObjectsContext} from "./views/model/view-objects-context";
import {StorageData} from "@component/diskmgmt/model/storage-data";

/**
 * This service orchestrates breadcrumb routing in disk manages view. For example initializing links and callbacks
 * for cluster and hosts; updating links and callbacks for cluster, hosts, groups and storage when switching to
 * virtual objects mode and vice versa.
 */
@Injectable()
export class DiskManagementRoutingService {

   private clusterNavigated: Subject<void> = new Subject<void>();
   onClusterNavigatedObs: Observable<void> = this.clusterNavigated.asObservable();

   private hostNavigated: Subject<HostData> = new Subject<HostData>();
   onHostNavigatedObs: Observable<HostData> = this.hostNavigated.asObservable();

   private virtualObjectsRequested: Subject<ViewObjectsContext> = new Subject<ViewObjectsContext>();
   onVirtualObjectsRequested: Observable<ViewObjectsContext> = this.virtualObjectsRequested.asObservable();

   // Clear errors only when clicking on breadcrumb links.
   private errorCleared: Subject<void> = new Subject<void>();
   onErrorCleared: Observable<void> = this.errorCleared.asObservable();

   hostList: HostData[];
   isVirtualObjectsView: boolean;

   @BreadcrumbNavigation()
   viewRoutes: {
      root: BreadcrumbRouteModel,
      hosts: BreadcrumbRouteModel,
      groups: BreadcrumbRouteModel,
      storage: BreadcrumbRouteModel,
   };

   setHostList(hostList: HostData[]): void {
      this.hostList = hostList;
   }

   setIsVirtualObjectsView(isVirtualObjectsView: boolean): void {
      this.isVirtualObjectsView = isVirtualObjectsView;
   }

   setInitialViewRoots(): void {
      this.viewRoutes = {
         root: BreadcrumbRouteModel.link(null, null, DiskManagementRoutingLabels.CLUSTER),
         hosts: BreadcrumbRouteModel.links(this.onHostNavigated.name),
         groups: BreadcrumbRouteModel.links(this.onVoGroupNavigated.name, this.isGroupDisabled),
         storage: BreadcrumbRouteModel.links(this.onVoStorageNavigated.name, this.isStorageDisabled),
      };
   }

   setHostDetailsLevelRootCb(): void {
      this.viewRoutes.root.onTrigger = this.onClusterNavigated.name;
   }

   private onClusterNavigated(): void {
      this.clusterNavigated.next();
   }

   private onHostNavigated(): void {
      this.hostNavigated.next(this.getHostBySelectedLink());
   }

   showBreadcrumbNavigation(): void {
      // Display the breadcrumb navigation
      VsanBreadcrumbService.show(true);
      // Navigate to the root route initially
      this.navigateToRootRoute();
   }

   navigateToHostRoute(hostLink: string): void {
      this.viewRoutes.hosts.select(hostLink);
      this.viewRoutes.hosts.navigate();
   }

   /**
    * The method handles view objects action for the corresponding disk mgmt entity as follows:
    * 1) Context properties are null. Navigate to cluster and "All hosts". Query cluster objects.
    * 2) Host - Only host is provided by the context. Navigate to host and "All groups".
    * Query host objects.
    * 3) Group - Host and group is provided by the context. Navigate to host, group and "All disks".
    * Query group objects.
    * 4) Storage - Host, group and storage is provided by the context. Navigate to host, group and storage.
    * Query storage objects.
    */
   viewObjects(context: ViewObjectsContext): void {
      if (context.host) {
         this.routeToVirtualObjectsHost(context.host);
      } else {
         this.routeToVirtualObjectsHost(null);
         this.queryClusterObjects();
         return;
      }
      if (context.group) {
         this.routeToVirtualObjectsGroups(this.getDiskGroupLabel(context.group));
      }
      if (context.storage) {
         this.routeToVirtualObjectsStorage(context.storage.name);
         this.queryStorageObjects();
      } else if (context.group) {
         this.routeToVirtualObjectsStorage();
         this.queryGroupObjects();
      } else {
         this.routeToVirtualObjectsGroups();
         this.queryHostObjects();
      }
   }

   // region Virtual objects view specific methods for breadcrumb routing to host, group and storage
   private routeToVirtualObjectsHost(host: HostData): void {
      const hostName: string = host?.name || DiskManagementRoutingLabels.ALL_HOSTS;
      this.viewRoutes.root.onTrigger = this.onVoClusterNavigated.name;
      this.updateHostLinks();
      this.viewRoutes.hosts.onTrigger = this.onVoHostNavigated.name;
      this.viewRoutes.hosts.isDisabled = this.isHostDisabled;
      this.viewRoutes.groups.isHidden = host?.isSingleTierEnabled;
      this.navigateToHostRoute(hostName);
   }

   private routeToVirtualObjectsGroups(groupLabel: string = DiskManagementRoutingLabels.ALL_GROUPS): void {
      const groupLinks: string[] = [DiskManagementRoutingLabels.ALL_GROUPS, ...this.getGroupLinks()];
      this.viewRoutes.groups.links = groupLinks;
      this.viewRoutes.groups.select(groupLabel);
      this.viewRoutes.groups.navigate();
   }

   private routeToVirtualObjectsStorage(storageName?: string): void {
      const storageLinks: string[] = [this.getAllStorageLabel(), ...this.getStorageLinks()];
      this.viewRoutes.storage.links = storageLinks;
      this.viewRoutes.storage.select(storageName ? storageName : this.getAllStorageLabel());
      this.viewRoutes.storage.navigate();
   }

   private getGroupLinks(): string[] {
      const host: HostData = this.getHostBySelectedLink();
      const usedGroups: DiskGroupData[] =
            host.diskGroups.filter(diskGroup => !DiskMgmtDiskGroupUtil.isUnusedDiskGroup(diskGroup));
      return usedGroups.map(diskGroup => this.getDiskGroupLabel(diskGroup));
   }

   private getStorageLinks(): string[] {
      const diskGroup: DiskGroupData = this.getGroupBySelectedLink();
      return this.getUsedGroupStorage(diskGroup).map(storage => storage.name);
   }
   // endregion

   // region Virtual objects view specific breadcrumb route callbacks on cluster, host, group and storage levels
   onVoClusterNavigated(): void {
      this.errorCleared.next();
      this.navigateToHostRoute(DiskManagementRoutingLabels.ALL_HOSTS);
      this.queryClusterObjects();
   }

   onVoHostNavigated(): void {
      this.errorCleared.next();
      if (this.viewRoutes.hosts.selectedLink === DiskManagementRoutingLabels.ALL_HOSTS) {
         this.queryClusterObjects();
         return;
      }

      // If vSAN max is enabled and host is selected,
      // preselect first group (hide it visualy) and all disks from it
      if (this.getHostBySelectedLink().isSingleTierEnabled) {
         this.routeToVirtualObjectsGroups(this.getGroupLinks()[0]);
         this.routeToVirtualObjectsStorage();
         this.viewRoutes.groups.isHidden = true;
      } else {
         this.routeToVirtualObjectsGroups();
      }

      this.queryHostObjects();
   }

   onVoGroupNavigated(): void {
      this.errorCleared.next();
      if (this.viewRoutes.groups.selectedLink === DiskManagementRoutingLabels.ALL_GROUPS) {
         this.queryHostObjects();
         return;
      }
      this.routeToVirtualObjectsStorage();
      this.queryGroupObjects();
   }

   onVoStorageNavigated(): void {
      this.errorCleared.next();
      if (this.viewRoutes.storage.selectedLink === this.getAllStorageLabel()) {
         this.queryGroupObjects();
         return;
      }
      this.queryStorageObjects();
   }

   // Disable only dropdown items with no objectUuids that represent certain disk mgmt entities.
   // For example "host1" with no objectUuids will be disabled while "All hosts" item cannot be
   // disabled because it is selected when the cluster is navigated.
   isHostDisabled = (link: string): boolean => this.hasNoPlacedObjects(this.findHostByName(link));

   isGroupDisabled = (link: string): boolean => this.hasNoPlacedObjects(this.findDiskGroupByLabel(link));

   isStorageDisabled = (link: string): boolean => this.hasNoPlacedObjects(this.findStorageByName(link));

   private hasNoPlacedObjects(entity: {objectUuids: string[]}): boolean {
      return entity && VsanUiUtils.isEmpty(entity.objectUuids);
   }
   // endregion

   // region Methods for querying virtual objects on cluster, host, group and storage levels
   private queryClusterObjects = (): void => this.queryObjects(null);

   private queryHostObjects = (): void =>
         this.queryObjects(new ViewObjectsContext(this.getHostBySelectedLink(), this.getPreselectedDiskGroup(), null));

   private queryGroupObjects = (): void =>
         this.queryObjects(new ViewObjectsContext(this.getHostBySelectedLink(), this.getGroupBySelectedLink(), null));

   private queryStorageObjects = (): void => this.queryObjects(new ViewObjectsContext(
         this.getHostBySelectedLink(),
         this.getGroupBySelectedLink(),
         this.getStorageBySelectedLink(),
   ));

   private getPreselectedDiskGroup = (): DiskGroupData | null => {
      return DiskMgmtDiskGroupUtil.getBreadcrumbPreselectedDiskGroup(this.getHostBySelectedLink());
   };

   private queryObjects = (viewObjectsContext: ViewObjectsContext): void =>
         this.virtualObjectsRequested.next(viewObjectsContext);
   // endregion

   // region Methods for getting host, disk group and storage by the corresponding link
   private getHostBySelectedLink(): HostData {
      return this.findHostByName(this.viewRoutes.hosts.selectedLink);
   }

   private findHostByName(hostName: string): HostData {
      return this.hostList && this.hostList.find(host => host.name === hostName);
   }

   private getGroupBySelectedLink(): DiskGroupData {
      return this.findDiskGroupByLabel(this.viewRoutes.groups.selectedLink);
   }

   private findDiskGroupByLabel(groupLabel: string): DiskGroupData {
      const host: HostData = this.getHostBySelectedLink();
      return host && host.diskGroups.find(diskGroup =>
            this.getDiskGroupLabel(diskGroup) === groupLabel);
   }

   private getDiskGroupLabel(diskGroup: DiskGroupData): string {
      return DiskMgmtDiskGroupUtil.getLabel(diskGroup, VsanDiskGroupLabelFormat.TYPE_AND_UUID);
   }

   private getStorageBySelectedLink(): StorageData {
      const diskData: DiskData | PmemDiskData = this.findStorageByName(this.viewRoutes.storage.selectedLink);
      return this.isSelectedGroupPMEM() ?
            StorageData.fromPmemDiskData(diskData as PmemDiskData) :
            StorageData.fromDiskData(diskData as DiskData);
   }

   private findStorageByName(storageName: string): DiskData | PmemDiskData {
      const diskGroup: DiskGroupData = this.getGroupBySelectedLink();
      return this.getUsedGroupStorage(diskGroup).find(storage => storage.name === storageName);
   }
   // endregion

   private getAllStorageLabel = (): string =>
         DiskManagementRoutingLabels.getAllStorage(this.isSelectedGroupPMEM());

   private isSelectedGroupPMEM = (): boolean => DiskMgmtDiskGroupUtil.isPmemDiskGroup(this.getGroupBySelectedLink());

   private getUsedGroupStorage(diskGroup: DiskGroupData): Array<DiskData | PmemDiskData> {
      if (!diskGroup) {
         return [];
      }
      if (DiskMgmtDiskGroupUtil.isPmemDiskGroup(diskGroup)) {
         return diskGroup.pmemStorage;
      } else if (!DiskMgmtDiskGroupUtil.isUnusedDiskGroup(diskGroup)) {
         return diskGroup.disks;
      }
      return [];
   }

   navigateToRootRoute(): void {
      this.viewRoutes.root.navigate();
   }

   resetHostsRoutes(): void {
      this.viewRoutes.hosts.onTrigger = this.onHostNavigated.name;
      this.viewRoutes.hosts.isDisabled = (): boolean => false;
      this.updateHostLinks();
   }

   resetRootCallBack(): void {
      this.viewRoutes.root.onTrigger = null;
   }

   updateHostLinks(): void {
      const hostLinks: string[] = this.hostList.map(host => host.name);
      this.viewRoutes.hosts.links = this.isVirtualObjectsView ?
            [DiskManagementRoutingLabels.ALL_HOSTS, ...hostLinks] :
            hostLinks;
   }
}

export class DiskManagementRoutingLabels {
   static readonly CLUSTER: string =
         VsanUiUtils.getString("vsan.manage.diskManagement.breadcrumb.cluster");
   static readonly ALL_HOSTS: string =
         VsanUiUtils.getString("vsan.manage.diskManagement.breadcrumb.host.allHosts");
   static readonly ALL_GROUPS: string =
         VsanUiUtils.getString("vsan.manage.diskManagement.breadcrumb.group.allGroups");
   static getAllStorage = (isPMEM: boolean): string => VsanUiUtils.getString(isPMEM ?
         "vsan.manage.diskManagement.breadcrumb.storage.allDevices" :
         "vsan.manage.diskManagement.breadcrumb.storage.allDisks");
}
