import {AfterViewInit, Component, Input} from "@angular/core";
import {ChartSectionData} from "@component/chart/model/chart-section.data";
import {VsanUiUtils} from "@util/vsan-util";
import {HighchartsThemeService} from "@component/chart/highcharts-theme.service";
import {TooltipOptions} from "highcharts/highcharts.src";
import {StorageCapacity} from "@generated/storage-capacity";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {TooltipValue} from "@util/tooltip-value";
import {BarChartService} from "@component/chart/capacity-bar-chart/bar-chart.service";
import {ReferenceWatcher} from "@util/reference-watcher";
import {CapacityType} from "@util/capacity-type";

/**
 * A component that represents capacity as:
 *    - Total label when total is present only (e.g., cache disk, unmounted/unhealthy disk/disk group).
 *    - Chart when used / reserved capacity is present.
 *  The component is used in disk mgmt view on disk, disk group and host level.
 */
@Component({
   selector: "vsan-storage-capacity",
   templateUrl: "storage-capacity.html",
   styleUrls: ["storage-capacity.scss"],
   providers: [ReferenceWatcher],
})
export class StorageCapacityComponent implements AfterViewInit {

   @Input()
   capacity: StorageCapacity;

   sectionsData: ChartSectionData[] = [];
   // Set width, as otherwise tooltips content can be wrapped (e.g., in host lists).
   // This behaves like a max-width + auto-resize.
   tooltipOptions: TooltipOptions = {style: {width: 200}};

   constructor(private highchartsThemeService: HighchartsThemeService,
         private barChartService: BarChartService,
         private refWatcher: ReferenceWatcher) {
      this.refWatcher.watchSubscriptionRef(
            this.highchartsThemeService.whenThemeChanged.subscribe(theme => this.updateSections()));
   }

   ngAfterViewInit(): void {
      // Timeout is required to properly resize the chart
      this.refWatcher.setTimeout(() => this.updateSections(), 0);
   }

   showOnlyTotalLabel(): boolean {
      return this.capacity && !this.capacity.used;
   }

   getTotalLabel(): string {
      return VsanUiUtils.formatFileSize(this.capacity.total);
   }

   private updateSections(): void {
      if (this.showOnlyTotalLabel()) {
         return;
      }

      const capacityColor: Map<CapacityType, string> = this.getCapacityColor();
      const capacityTooltip: Map<string, TooltipValue> = DiskMgmtUtil.getCapacityTooltip(this.capacity, capacityColor);

      this.sectionsData = [];
      if (!VsanUiUtils.isUnset(this.capacity.used)) {
         this.sectionsData.push(new ChartSectionData(
               VsanUiUtils.getString("vsan.manage.diskManagement.capacity.used.label"),
               this.capacity.used,
               "diskManagementUsed",
               true,
               capacityColor.get(CapacityType.USED),
               capacityTooltip));
      }
   }

   private getCapacityColor(): Map<CapacityType, string> {
      return new Map<CapacityType, string>([
               [CapacityType.USED, this.barChartService.getColorPalette()[0]],
               [CapacityType.FREE, this.highchartsThemeService.getCapacityFreeColor()]]);
   }
}
