import {Component} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {DiskMappingSpec} from "@generated/disk-mapping-spec";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";

@Component({
   templateUrl: "create-group.html",
   selector: "vsan-diskmgmt-create-group",
})
export class CreateGroupComponent {

   clusterRef: ManagedObject;
   hostRef: ManagedObject;
   hostName: string;
   isWitnessHost: boolean;
   diskMapping: VsanDiskMapping;

   @LoadingIndicator({name: [LoaderName.ON_INIT, LoaderName.ON_CONFIRM]})
   busy: boolean;

   // This flag is needed, because `busy`'s value can't be changed, because of the LoadingIndicator
   dataLoaded: boolean;

   alertMessages: string[] = [];

   // Flag to keep whether the user tried to submit the dialog.
   isValidated: boolean = false;

   constructor(private diskGroupMutationService: DiskGroupMutationService) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.dialog.createDiskGroup.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   ngOnInit(): void {
      this.alertMessages = [];

      const context: any = VsanUiUtils.getModalContext();
      this.clusterRef = ManagedObject.fromUid(context.clusterId);
      this.hostRef = ManagedObject.fromUid(context.hostId);
      this.hostName = context.hostName;
      this.isWitnessHost = context.isWitnessHost;
   }

   dataLoad = (): void => {
      this.dataLoaded = true;
   };

   configValidate = (errors: string[]) => {
      this.alertMessages = errors;
   };

   configChange = (diskMapping: VsanDiskMapping) => {
      this.diskMapping = diskMapping;
   };

   @Loader({name: LoaderName.ON_CONFIRM})
   async onOkClicked(): Promise<void> {
      this.isValidated = true;
      if (this.alertMessages.length > 0) {
         return;
      }

      const spec: DiskMappingSpec = DiskMappingSpec.Factory.create(
            this.clusterRef,
            [this.diskMapping]);

      const result: ManagedObject = await this.diskGroupMutationService.createDiskGroup(this.hostRef, spec);
      VsanUiUtils.closeModalDialog(result);
   }

   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private handleError(error: string): void {
      this.alertMessages = [error];
   }
}
