import {Component, OnInit} from "@angular/core";
import {ScsiDisk} from "@component/diskmgmt/model/scsi-disk.data";
import {DecommissionModeSummary} from "@component/precheck/pre-check-evacuation-summary.component";
import {DecommissionMode} from "@generated/decommission-mode";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {UnmountDiskGroupSpec} from "@generated/unmount-disk-group-spec";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VsanPropertyProvider} from "@generated/vsan-property-provider";
import {VsanQueryDataEvacuationInfoSpec} from "@generated/vsan-query-data-evacuation-info-spec";
import {DiskGroupActionData} from "@model/disk-group-action.data";
import {HostActionData} from "@model/host-action.data";
import {DiskGroupActionContext} from "@navigation/context/disk-group-action-context";
import {EvacuationContext} from "@navigation/context/evacuation.context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {HostSystem} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {Logger} from "@util/logger";
import {VsanDataMigrationUtil} from "@util/vsan-data-migration-util";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * This component is used to unmount disk group.
 */
@Component({
   templateUrl: "unmount-disk-group.html",
   selector: "vsan-unmount-disk-group",
   styleUrls: ["../actions-common.scss"],
})
export class UnmountDiskGroupComponent implements OnInit {
   @LoadingIndicator({
      name: [
         LoaderName.ON_INIT, LoaderName.DATA_EVACUATION, LoaderName.ON_CONFIRM],
   })
   loading: boolean;

   alerts: string[] = [];
   warningMessages: string[] = [];
   host: HostActionData;
   diskGroup: DiskGroupActionData;

   dataOnDiskGroupLabel: string = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabel");
   evacuationEntityUuid: string;
   shouldAllowDataMigration: boolean;
   isValidDecommisionMode: boolean = true;
   selectedDecommissionMode: DecommissionMode = DecommissionMode.ensureObjectAccessibility;
   goToPrecheckButtonText: string;
   isDiskResourcePrecheckSupported: boolean = true;
   preselectedDecommissionMode: DecommissionMode;
   localizedPreselectedDecommissionMode: string;
   preCheckRecommendationMessage: string = VsanUiUtils.getString(
         "vsan.dialog.precheckEvacuation.recommendationMesaage.diskGroup.unmount");

   constructor(private diskGroupMutationService: DiskGroupMutationService,
         private vsanPropertyProvider: VsanPropertyProvider,
         private capabilityProvider: VsanCapabilityProvider,
         private navigationService: NavigationService,
         private dataService: DataServiceClient) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.actions.unmountDiskGroup.dialogTitle",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      const context: DiskGroupActionContext = VsanUiUtils.getModalContext();
      this.host = context.host;
      this.diskGroup = context.diskGroup;
      this.preselectedDecommissionMode = context.decommissionMode;
      this.localizedPreselectedDecommissionMode =
            VsanDataMigrationUtil.getLocalizedDecommissionMode(this.preselectedDecommissionMode);

      this.host.isInMaintenanceMode = await this.dataService
            .getProperty(HostSystem.runtime.inMaintenanceMode, this.host.hostRef);
      this.initUI();
   }

   @ErrorHandler({name: LoaderName.ON_INIT})
   private handleInitError(): void {
      this.alerts = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
   }

   private async initUI(): Promise<void> {
      if (this.preselectedDecommissionMode) {
         // TODO: Display DecommissionMode as a label instead of a select dropdown.
      }

      this.evacuationEntityUuid = this.diskGroup.vsanUuid;
      this.requestDataEvacuationProperty(this.diskGroup.diskMapping.ssd);

      try {
         const isApiSupported: boolean =
               await this.capabilityProvider.getIsUnmountWithMaintenanceModeSupported(this.host.hostRef);
         this.setDialogContent(isApiSupported);
      } catch (error) {
         Logger.error(error);
         this.alerts = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
      }
   }

   private setDialogContent = (isApiSupported: boolean) => {
      this.shouldAllowDataMigration = isApiSupported && VsanDiskMgmtActionsUtil.shouldAllowDiskGroupDataMigration(
            this.host, this.diskGroup);
      if (!this.shouldAllowDataMigration) {
         this.selectedDecommissionMode = DecommissionMode.noAction;
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_SMALL);
      }

      if (isApiSupported) {
         const headerMessage: string = this.getHeaderMessage();
         if (headerMessage) {
            this.warningMessages.push(headerMessage);
         }
         const dataEvacuationWarningMessage: string = VsanDiskMgmtActionsUtil.getDataMigrationWarningForDiskGroup(
               this.host.isInMaintenanceMode, this.diskGroup.isMounted);
         if (dataEvacuationWarningMessage) {
            this.warningMessages.push(dataEvacuationWarningMessage);
         }
      } else {
         this.warningMessages = [VsanUiUtils.getString("vsan.actions.unmountDiskGroup.maintenanceMode.notSupported")];
      }
   };

   private getHeaderMessage = (): string => {
      if (this.host.isInMaintenanceMode) {
         return VsanUiUtils.getString("vsan.actions.unmountDiskGroup.dialogContent.headerDataEvacuation");
      } else if (this.host.isWitnessHost) {
         return VsanUiUtils.getString(
               "vsan.actions.unmountDiskGroup.dialogContent.headerDataEvacuation.externalWitnessHostWarning");
      } else if (this.diskGroup.isMounted) {
         return VsanUiUtils.getString(
               "vsan.actions.unmountDiskGroup.dialogContent.headerDataEvacuation.withMaintenanceMode");
      }
   };

   @Loader({name: LoaderName.DATA_EVACUATION})
   private async requestDataEvacuationProperty(disk: ScsiDisk): Promise<void> {
      if (!DiskMgmtDiskUtil.isScsiDiskHealthy(disk)) {
         this.dataOnDiskGroupLabel = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabel.error");
         return;
      }

      const result: number = await this.vsanPropertyProvider.getVsanDataEvacuationInfo(
            this.host.hostRef,
            VsanQueryDataEvacuationInfoSpec.Factory.create([disk]));
      this.onDataEvacuationPropertyRetrieved(result);
   }

   private onDataEvacuationPropertyRetrieved = (result: number) => {
      if (result >= 0) {
         const formattedSize: string = VsanUiUtils.formatFileSize(result);
         this.dataOnDiskGroupLabel = VsanUiUtils.getString(
               "vsan.dialog.deleteDiskGroup.evacuateDataLabelWithSize",
               formattedSize);
      }
   };

   @ErrorHandler({name: LoaderName.DATA_EVACUATION})
   private onDataRetrievalError(error: any): void {
      Logger.error("Unable to fetch disk group evacuation data: ", error);
      this.alerts = [VsanUiUtils.getString("vsan.common.error.dataExtraction")];
      this.dataOnDiskGroupLabel = VsanUiUtils.getString("vsan.dialog.deleteDiskGroup.evacuateDataLabel.error");
   }

   onDecommisionModeSummaryChanged = (summary: DecommissionModeSummary) => {
      this.selectedDecommissionMode = summary.value;
      this.isValidDecommisionMode = summary.isValidDecommissionMode;
      if (VsanDiskMgmtActionsUtil.isPrecheckWithObjects(summary) && !this.isDiskResourcePrecheckSupported) {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_LARGE);
      } else if (this.isDiskResourcePrecheckSupported) {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_PRECHECK);
      } else {
         VsanUiUtils.setModalDialogHeight(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_GROUP_DIALOG_HEIGHT_MEDIUM);
      }
   };

   onDiskResourcePrecheckSupportedChanged = (isSupported: boolean) => {
      this.isDiskResourcePrecheckSupported = isSupported;
      // Preselecting decommmission mode means that this dialog is opened from Data migration pre-check page,
      // in which case GO TO PRE-CHECK button doesn't need to be display.
      this.goToPrecheckButtonText = !this.preselectedDecommissionMode && this.isDiskResourcePrecheckSupported ?
            VsanUiUtils.getString("vsan.actions.goToPrecheckDataMigration.label") : "";
   };

   @Loader({name: LoaderName.ON_CONFIRM})
   async ok(): Promise<void> {
      this.alerts = [];

      const decommissionMode: DecommissionMode = this.preselectedDecommissionMode ?
            this.preselectedDecommissionMode :
            VsanDiskMgmtActionsUtil.getSelectedDecommissionMode(
                  this.shouldAllowDataMigration, this.selectedDecommissionMode);
      const spec: UnmountDiskGroupSpec = UnmountDiskGroupSpec.Factory.create(
            this.diskGroup.diskMapping, decommissionMode);

      const task: ManagedObject = await this.diskGroupMutationService
            .unmountDiskGroup(ManagedObject.contextObject, this.host.hostRef, spec);
      this.closeDialog(task);
   }

   onGoToPrecheckClicked = () => {
      const evacuationContext: EvacuationContext = VsanDiskMgmtActionsUtil.getEvacuationContext(
            this.host.hostRef, this.evacuationEntityUuid, this.selectedDecommissionMode);
      this.navigationService.navigateToAndCloseModal(PlatformExtension.CLUSTER_DATA_MIGRATION_PRE_CHECK,
            ManagedObject.contextObjectUid, evacuationContext);
   };

   private closeDialog = (task: ManagedObject) => {
      VsanUiUtils.closeModalDialog(task);
   };

   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private handleError(error: any): void {
      this.alerts = [error];
   }
}
