import {VsanBreadcrumbService} from "./vsan-breadcrumb.service";

export class BreadcrumbRouteModel {
   /**
    * Keeps the route key. It is needed for routes ordering ex.
    */
   private _key: string;

   /**
    * Keeps route links values. It could be only 1 or multiple links displayed as dropdown.
    */
   private _links: string[] = [];

   /**
    * Currently selected link. Very useful when there is multiple links for 1 route.
    */
   private _selectedLink: string;

   /**
    * Component callback method name. When the route gets triggered, then this callback is called as well.
    */
   onTrigger: string;

   /**
    * A predicate that defines disabled state of links from dropdown.
    */
   isDisabled: IsDisabledLink;

   /**
    * Indicates whether a given route model should be hidden
    */
   isHidden: boolean;

   static link(callback: string = "", isDisabled: IsDisabledLink = () => false, value: string = ""):
         BreadcrumbRouteModel {
      return new BreadcrumbRouteModel(callback, isDisabled, [value]);
   }

   static links(callback: string, isDisabled: IsDisabledLink = () => false, values: string[] = []):
         BreadcrumbRouteModel {
      return new BreadcrumbRouteModel(callback, isDisabled, values);
   }

   constructor(callback: string, isDisabled: IsDisabledLink, values: string[] = []) {
      this.onTrigger = callback;
      this.isDisabled = isDisabled;
      this._links = values;
   }

   get key(): string {
      return this._key;
   }

   set key(key: string) {
      this._key = key;
   }

   get link(): string {
      return this._links[0];
   }

   set link(value: string) {
      this._links[0] = value;
      this._selectedLink = value;
   }

   get links(): string[] {
      return this._links;
   }

   set links(values: string[]) {
      this._links = values;
   }

   get selectedLink(): string {
      return this._selectedLink;
   }

   get isList(): boolean {
      return this._links.length > 1;
   }

   select(value: string) {
      if (this._links.indexOf(value) === -1) {
         return;
      }
      this._selectedLink = value;
   }

   navigate() {
      VsanBreadcrumbService.route.next(this);
   }
}

export type IsDisabledLink = (link: string) => boolean;
