import {DiskGroupData} from "@generated/disk-group-data";
import {DiskData} from "@generated/disk-data";
import {PmemDiskData} from "@generated/pmem-disk-data";
import {DiskGroupType} from "@generated/disk-group-type";
import {StorageData} from "@component/diskmgmt/model/storage-data";
import {ClaimableState} from "@util/disk-mgmt.util";
import {HostData} from "@generated/host-data";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {StorageCapacity} from "@generated/storage-capacity";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {StorageIdentifier} from "@component/diskmgmt/model/storage-identifier";

export class StorageGroupData implements DiskGroupData {
   name: string;
   iconId: string;
   isMounted: boolean;
   disks: DiskData[];
   pmemStorage: PmemDiskData[];
   ownerHostRef: any;
   diskGroupType: DiskGroupType;
   isLocked: boolean;
   capacity: StorageCapacity;
   objectUuids: string[];
   storageData: StorageData[];

   static fromDiskGroup(diskGroupData: DiskGroupData): StorageGroupData {
      const storageGroup: StorageGroupData = new StorageGroupData();
      storageGroup.name = diskGroupData.name;
      storageGroup.iconId = diskGroupData.iconId;
      storageGroup.isMounted = diskGroupData.isMounted;
      storageGroup.disks = diskGroupData.disks;
      storageGroup.pmemStorage = diskGroupData.pmemStorage;
      storageGroup.ownerHostRef = diskGroupData.ownerHostRef;
      storageGroup.diskGroupType = diskGroupData.diskGroupType;
      storageGroup.isLocked = diskGroupData.isLocked;
      storageGroup.capacity = diskGroupData.capacity;
      storageGroup.objectUuids = diskGroupData.objectUuids;
      if (!DiskMgmtDiskGroupUtil.isUnusedDiskGroup(diskGroupData)) {
         storageGroup.storageData =
               StorageGroupData.getStorageItemsFromDiskData(diskGroupData.disks, ClaimableState.IN_USE).concat(
               StorageGroupData.getStorageItemsFromPmemData(diskGroupData.pmemStorage, ClaimableState.IN_USE));
      }
      return storageGroup;
   }

   static setUnusedStorageData(host: HostData, storageGroup: StorageGroupData) {
      if (!host || !storageGroup) {
         return;
      }
      const eligibleDisks: DiskData[] = DiskMgmtHostUtil.getDisks(host, ClaimableState.USABLE);
      const ineligibleDisks: DiskData[] = DiskMgmtHostUtil.getDisks(host, ClaimableState.NOT_USABLE);
      const eligiblePmemStorage: PmemDiskData[] = DiskMgmtHostUtil.getPmemStorage(host, ClaimableState.USABLE);
      const ineligiblePmemStorage: PmemDiskData[] = DiskMgmtHostUtil.getPmemStorage(host, ClaimableState.NOT_USABLE);
      storageGroup.storageData =
             StorageGroupData.getStorageItemsFromDiskData(eligibleDisks, ClaimableState.USABLE).concat(
             StorageGroupData.getStorageItemsFromDiskData(ineligibleDisks, ClaimableState.NOT_USABLE)).concat(
             StorageGroupData.getStorageItemsFromPmemData(eligiblePmemStorage, ClaimableState.USABLE)).concat(
             StorageGroupData.getStorageItemsFromPmemData(ineligiblePmemStorage, ClaimableState.NOT_USABLE));
   }

   private static getStorageItemsFromDiskData(disks: DiskData[], claimableState: ClaimableState): StorageData[] {
      if (!disks) {
         return [];
      }
      return disks.map(disk => StorageData.fromDiskData(disk, claimableState));
   }

   private static getStorageItemsFromPmemData(
         pmemStorage: PmemDiskData[], claimableState: ClaimableState): StorageData[] {
      if (!pmemStorage) {
         return [];
      }
      return pmemStorage.map(storage => StorageData.fromPmemDiskData(storage, claimableState));
   }
}