import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {HostData} from "@generated/host-data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {LoadingAwareProperty} from "@model/loading-aware-property";
import {EditVsanManagedDiskClaimContext} from "@navigation/context/edit-vsan-managed-disk-claim-context";
import {ManagedObject} from "@service/managed-object";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {ModalBuilder} from "@util/modal-builder";
import {ActionAvailableStatus} from "@util/validation/action-available-status";
import {VsanDialogProperties, VsanDialogUtil} from "@util/vsan-dialog-util";
import {VsanRoutes} from "../../../../vsan-routes";
import {ViewObjectsContext} from "../model/view-objects-context";
import {DiskManagementActionAvailabilityService} from "../validation/disk-management-action-availability.service";
import {DiskManagementActionContext} from "../validation/disk-management-action-context";

@Component({
   selector: "vsan-cluster-actions",
   templateUrl: "cluster-actions.html",
   styleUrls: ["cluster-actions.scss"],
   host: {class: "vsan-actions"},
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ClusterActionsComponent {

   @Input()
   autoClaimMode: boolean;

   @Input()
   vsanConfig: VsanConfigSpec;

   @Input()
   clusterName: string;

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   @Input()
   hasDuplicatePciIdDevices: LoadingAwareProperty<boolean>;

   private _allHosts: HostData[];
   @Input()
   set allHosts(value: HostData[]) {
      this._allHosts = value;
      this.clusterEligibleDiskCount = DiskMgmtHostUtil.getEligibleDiskCount(value);
   }

   get allHosts(): HostData[] {
      return this._allHosts;
   }

   clusterEligibleDiskCount: number = 0;

   DiskManagementActionAvailabilityService: typeof DiskManagementActionAvailabilityService =
         DiskManagementActionAvailabilityService;

   constructor(private diskManagementActionAvailabilityService: DiskManagementActionAvailabilityService) {}

   actionDisabled(actionId: string): boolean {
      return !this.getActionStatus(actionId).available;
   }

   getActionButtonDisabledReason(actionId: string): string {
      if (actionId === DiskManagementActionAvailabilityService.VIEW_DUPLICATE_PCI_ID_DEVICES &&
            this.hasDuplicatePciIdDevices.isLoading) {
         return null;
      }
      return this.getActionStatus(actionId).message;
   }

   private getActionStatus(actionId: string): ActionAvailableStatus {
      return this.diskManagementActionAvailabilityService.checkAvailability(this.getActionValidationContext(actionId));
   }

   private getActionValidationContext(actionId: string): DiskManagementActionContext {
      return {
         actionId: actionId,
         hasEditPermission: this.hasEditPermission,
         vsanConfig: this.vsanConfig,
         isEmptyCluster: !this.allHosts?.length,
         autoClaimMode: this.autoClaimMode,
         allHosts: this.allHosts,
         clusterCapabilities: this.clusterCapabilities,
         hasHostMaintenancePermission: null,
         spaceEfficiencyConfig: null,
      };
   }

   invokeSemiAutoClaim(): void {
      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/semiAutoClaim`)
            .setSize(VsanDialogUtil.CLAIM_DISKS_ACTION_DIALOG.width,
                  VsanDialogUtil.CLAIM_DISKS_ACTION_DIALOG.width)
            .open()
            .then((tasks: ManagedObject[]) =>
                  this.diskManagementActionAvailabilityService.taskStarted.next(tasks));
   }

   viewDuplicatePciIdDevices(): void {
      if (this.hasDuplicatePciIdDevices.isLoading) {
         return;
      }
      new ModalBuilder(`${VsanRoutes.UPDATE_VIEW}/updateDuplicatePciIdDevices`)
            .setSize(VsanDialogUtil.DUPLICATE_PCI_ID_MODEL_SELECTION_DIALOG.width,
                  VsanDialogUtil.DUPLICATE_PCI_ID_MODEL_SELECTION_DIALOG.height)
            .open();
   }

   editVsanDiskManagement(): void {
      new ModalBuilder(`${VsanRoutes.SERVICES_VIEW}/editVsanManagedDiskClaim`)
            .setSize(VsanDialogUtil.VSAN_DISK_CLAIM_ACTION_DIALOG.width,
                  VsanDialogUtil.VSAN_DISK_CLAIM_ACTION_DIALOG.height)
            .open({clusterName: this.clusterName} as EditVsanManagedDiskClaimContext)
            .then((taskRef: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([taskRef]);
            });
   }

   viewClusterObjects = (): void => this.diskManagementActionAvailabilityService.objectsViewSelected
         .next(new ViewObjectsContext(null, null, null));
}