import {
   AfterViewInit,
   Directive, ElementRef, HostListener, Input,
} from "@angular/core";
import {ReferenceWatcher} from "@util/reference-watcher";

/**
 * A directive that provides keyboard navigation for clr-dropdown menus using Arrow keys and Tab / Shift + Tab.
 */
@Directive({
   selector: "[vsan-dropdown-navigation]",
})
export class DropdownNavigationDirective implements AfterViewInit {
   constructor(private el: ElementRef, private refWatcher: ReferenceWatcher) { }

   /**
    * Nesting level of the dropdown controls.
    */
   @Input("vsan-dropdown-navigation")
   nestingLevel: number = 1;

   ngAfterViewInit(): void {
      setTimeout(() => this.focusElements(this.el.nativeElement, this.nestingLevel), 0);
   }

   @HostListener("keydown", ["$event"])
   onKeydown(e: KeyboardEvent): void {
      const ARROW_DOWN_KEY: string = "ArrowDown";
      const ARROW_UP_KEY: string = "ArrowUp";
      const TAB_KEY: string = "Tab";

      let el: HTMLElement = document.activeElement as HTMLElement;
      if (![TAB_KEY, ARROW_DOWN_KEY, ARROW_UP_KEY].includes(e.key)) {
         if (e.key === "Enter") {
            el.click();
         }
         return;
      }

      const isBackwards: boolean = e.key === ARROW_UP_KEY || (e.key === TAB_KEY && e.shiftKey);
      el = this.getNextActive(el, isBackwards);

      if (el?.focus) {
         el.focus();
      }

      e.preventDefault();
      e.stopImmediatePropagation();
      e.stopPropagation();
   }

   private focusElements(target: HTMLElement, nestingLevel: number): void {
      if (nestingLevel === 0) {
         // Focus the first not disabled button.
         let el: HTMLElement = target.children[0] as HTMLElement;
         if ((el as HTMLSelectElement)?.disabled) {
            el = this.getNextActive(el);
         }
         el.focus();
         return;
      }

      this.focusElements(target.children[0] as HTMLElement, nestingLevel - 1);
   }

   private getNextActive(el: HTMLElement, isBackwards: boolean = false): HTMLElement {
      let getNext: (current: HTMLElement) => HTMLElement;
      if (!isBackwards) {
         getNext = (current: HTMLElement) => current ? current.nextElementSibling as HTMLElement : null;
      } else {
         getNext = (current: HTMLElement) => current ? current.previousElementSibling as HTMLElement : null;
      }

      el = getNext(el);
      while ((el as HTMLSelectElement)?.disabled) {
         const next: HTMLElement = getNext(el);
         if (!next) {
            break;
         }
         el = next;
      }

      return el;
   }
}