import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "mark-local-prompt.html",
   selector: "vsan-diskmgmt-mark-local",
})
export class MarkLocalComponent implements OnInit{

   isLocal: boolean;
   title: string;

   ngOnInit(): void {
      this.isLocal = VsanUiUtils.getModalContext();

      this.title = VsanUiUtils.getString(this.isLocal
            ? "storage.adapters.devices.action.markAsLocal"
            : "storage.adapters.devices.action.markAsRemote");
   }

}