import {Component} from "@angular/core";
import {EnumeratedFilterSelectionResolver} from "@component/datagrid/filter/enumerated-filter.component";
import {DiskMgmtFilterComponent} from "./disk-mgmt-filter.component";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {StorageData} from "@component/diskmgmt/model/storage-data";

@Component({
   templateUrl: "disk-mgmt-filter.html",
   selector: "vsan-storage-group-details-filter-device-operational-state",
   styleUrls: ["disk-mgmt-filter.scss"],
})
export class StorageGroupDetailsFilterDeviceOperationalStateComponent extends DiskMgmtFilterComponent<StorageData> {

   readonly comparator: EnumeratedFilterSelectionResolver<StorageData, string> =
         new class extends EnumeratedFilterSelectionResolver<StorageData, string> {
            isSelected(item: StorageData, selectedStateKeys: string[]): boolean {
               return selectedStateKeys.some(state =>
                     state === DiskMgmtDiskUtil.localizeOperationalState(item.deviceState));
            }
         }();
}