import {Component, Input} from "@angular/core";
import {StorageData} from "@component/diskmgmt/model/storage-data";
import {PreCheckEvacuationDialogSize} from "@component/precheck/pre-check-evacuation-dialog-size";
import {DiskData} from "@generated/disk-data";
import {DiskGroupData} from "@generated/disk-group-data";
import {DiskGroupMutationService} from "@generated/disk-group-mutation-service";
import {DiskGroupType} from "@generated/disk-group-type";
import {DiskLocalityType} from "@generated/disk-locality-type";
import {DiskType} from "@generated/disk-type";
import {HostData} from "@generated/host-data";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {DiskGroupActionData} from "@model/disk-group-action.data";
import {HostActionData} from "@model/host-action.data";
import {DeleteDisksContext} from "@navigation/context/delete-disks-context";
import {DeleteVsanDirectDisksContext} from "@navigation/context/delete-vsan-direct-disks-context";
import {EvacuationContext} from "@navigation/context/evacuation.context";
import {MountDiskContext} from "@navigation/context/mount-disk-context";
import {UnmountDiskContext} from "@navigation/context/unmount-disk-context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {ClaimableState} from "@util/disk-mgmt.util";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {ActionAvailableStatus} from "@util/validation/action-available-status";
import {VsanDiskMgmtActionsUtil} from "@util/vsan-disk-mgmt-actions-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanRoutes} from "../../../../vsan-routes";
import {ViewObjectsContext} from "../model/view-objects-context";
import {DiskManagementActionAvailabilityService} from "../validation/disk-management-action-availability.service";
import {DiskManagementActionContext} from "../validation/disk-management-action-context";
import {DedupEnabledWarningComponent} from "./deletediskgroup/dedup-enabled-warning.component";

@Component({
   selector: "vsan-storage-actions",
   templateUrl: "storage-actions.html",
   styleUrls: ["storage-actions.scss"],
   host: {class: "vsan-actions"},
})
export class StorageActionsComponent {
   DiskManagementActionAvailabilityService: typeof DiskManagementActionAvailabilityService =
         DiskManagementActionAvailabilityService;

   @Input()
   autoClaimMode: boolean;

   @Input()
   spaceEfficiencyConfig: SpaceEfficiencyConfig = SpaceEfficiencyConfig.NONE;

   @Input()
   hasEditPermission: boolean = false;

   @Input()
   hasHostMaintenancePermission: boolean = false;

   @Input()
   clusterCapabilities: VsanCapabilityData;

   @Input()
   claimableState: ClaimableState = ClaimableState.IN_USE;

   @Input()
   selectedStorage: StorageData;

   @Input()
   host: HostData;

   @Input()
   diskGroup: DiskGroupData;

   constructor(private diskGroupSvc: DiskGroupMutationService,
         private navigationService: NavigationService,
         private spaceEfficiencyService: SpaceEfficiencyService,
         private diskManagementActionAvailabilityService: DiskManagementActionAvailabilityService) {
   }

   get selectedDisk(): DiskData {
      return this.selectedStorage && !this.isSelectedStoragePmem() && this.selectedStorage.storage as DiskData;
   }

   get isInUse(): boolean {
      return this.claimableState === ClaimableState.IN_USE;
   }

   actionDisabled = (actionId: string): boolean => {
      return !this.getActionStatus(actionId).available;
   };

   getActionButtonTitle = (actionId: string): string => {
      return this.getActionStatus(actionId).message;
   };

   private isVsanClaimedDisk(): boolean {
      return DiskMgmtDiskUtil.isClaimedForVsan(this.selectedDisk.claimOption);
   }

   isVsanMaxClaimedDisk(): boolean {
      return DiskMgmtDiskGroupUtil.isVsanMaxSingleTier(this.diskGroup);
   }

   private getActionStatus = (actionId: string): ActionAvailableStatus => {
      return this.diskManagementActionAvailabilityService.checkAvailability(this.getActionValidationContext(actionId));
   };

   private getActionValidationContext(actionId: string): DiskManagementActionContext {
      return {
         actionId: actionId,
         hasEditPermission: this.hasEditPermission,
         vsanConfig: null,
         isEmptyCluster: null,
         autoClaimMode: this.autoClaimMode,
         diskGroup: this.diskGroup,
         host: this.host,
         storageData: this.selectedStorage,
         clusterCapabilities: this.clusterCapabilities,
         hasHostMaintenancePermission: this.hasHostMaintenancePermission,
         spaceEfficiencyConfig: this.spaceEfficiencyConfig,
      };
   }

   turnLed = (on: boolean): void => {
      this.diskGroupSvc.setDiskLedState(this.host.hostRef, [this.selectedDisk.uuid], on)
            .then(taskRef => {
               Logger.info("Turning LED indicator " + (on ? "on" : "off") + " for disk:", this.selectedDisk.uuid);
            });
   };

   invokePreCheckEvacuation = (): void => {
      if (this.clusterCapabilities.isDiskResourcePrecheckSupported) {
         const evacuationContext: EvacuationContext = VsanDiskMgmtActionsUtil.getEvacuationContext(
               this.host.hostRef, this.selectedDisk.vsanUuid);
         this.navigationService.navigateTo(PlatformExtension.CLUSTER_DATA_MIGRATION_PRE_CHECK,
               ManagedObject.contextObject, evacuationContext);
         return;
      }

      const context: any = {
         objectName: this.selectedDisk.name,
         objectUid: this.selectedDisk.vsanUuid,
         hostMor: this.host.hostRef,
      };

      new ModalBuilder("preCheckEvacuation")
            .setSize(PreCheckEvacuationDialogSize.WIDTH, PreCheckEvacuationDialogSize.HEIGHT_WITH_OBJECTS)
            .open(context);
   };

   /**
    * This method is used to delete the disk the user selected.
    */
   invokeDeleteDisk = (): void => {
      if (this.spaceEfficiencyService.isOnlyAllFlashSupportedForRemovingDisk(this.spaceEfficiencyConfig) &&
            this.diskGroup.diskGroupType === DiskGroupType.ALL_FLASH) {
         new ModalBuilder("diskManagement/deleteDiskUnsupportedDialog")
               .setSize(580, DedupEnabledWarningComponent.DIALOG_DELETE_DISK_HEIGHT_SMALL)
               .open();
         return;
      }

      const context: DeleteDisksContext = {
         disks: [this.selectedDisk],
         host: HostActionData.fromHostData(this.host),
         diskGroups: this.getDiskGroupActionData(),
         isVsanMaxEnabled: this.isVsanMaxClaimedDisk(),
      };

      const height: number = this.actionDisabled(DiskManagementActionAvailabilityService.DISK_PRE_CHECK_ID)
            ? VsanDiskMgmtActionsUtil.DELETE_DISKS_HEIGHT_SMALL
            : VsanDiskMgmtActionsUtil.DELETE_DISK_GROUP_HEIGHT_MEDIUM;

      new ModalBuilder("diskManagement/deleteDisks")
            .setSize(VsanDiskMgmtActionsUtil.DELETE_DISKS_WIDTH, height)
            .open(context)
            .then((tasks: ManagedObject[]) => {
               this.diskManagementActionAvailabilityService.taskStarted.next(tasks);
            });
   };

   invokeDeleteVsanDirectDisk(): void {
      const context: DeleteVsanDirectDisksContext = {
         disks: [this.selectedDisk],
      };

      new ModalBuilder("diskManagement/deleteVsanDirectDisks")
            .setSize(
                  VsanDiskMgmtActionsUtil.DELETE_VSAN_DIRECT_DISKS_WIDTH,
                  VsanDiskMgmtActionsUtil.DELETE_VSAN_DIRECT_DISKS_HEIGHT)
            .open(context)
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   }

   mountDisk(): void {
      const context: MountDiskContext = {
         diskName: this.selectedDisk.name,
         diskCanonicalName: this.selectedDisk.disk?.canonicalName,
         hostRef: this.host.hostRef,
      };

      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/mountStoragePoolDisk`)
            .setSize(VsanDiskMgmtActionsUtil.MOUNT_DISK_GROUP_DIALOG_WIDTH,
                  VsanDiskMgmtActionsUtil.MOUNT_DISK_GROUP_DIALOG_HEIGHT)
            .open(context)
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   }

   unmountDisk(): void {
      const context: UnmountDiskContext = {
         disk: this.selectedDisk,
         host: HostActionData.fromHostData(this.host),
      };

      new ModalBuilder(`${VsanRoutes.DISK_MANAGEMENT_VIEW}/unmountStoragePoolDisk`)
            .setSize(VsanDiskMgmtActionsUtil.UNMOUNT_DISK_DIALOG_WIDTH,
                  VsanDiskMgmtActionsUtil.UNMOUNT_DISK_DIALOG_HEIGHT)
            .open(context)
            .then((task: ManagedObject) => {
               this.diskManagementActionAvailabilityService.taskStarted.next([task]);
            });
   }

   get markSsdAvailable(): boolean {
      if (!this.selectedDisk || this.claimableState === ClaimableState.IN_USE || this.host.isSingleTierEnabled) {
         return false;
      }
      return !this.selectedDisk.isFlash;
   }

   get markHddAvailable(): boolean {
      if (!this.selectedDisk || this.claimableState === ClaimableState.IN_USE || this.host.isSingleTierEnabled) {
         return false;
      }
      return this.selectedDisk.isFlash;
   }

   markSsd = (ssd: boolean): void => {
      new ModalBuilder("diskManagement/markSsd")
            .setSize(576, ssd ? 240 : 180)
            .open(ssd)
            .then(result => {
               this.diskGroupSvc.setDiskType(this.host.hostRef, [this.selectedDisk.uuid], ssd)
                     .then((tasks: ManagedObject[]) => {
                        this.diskManagementActionAvailabilityService.taskStarted.next(tasks);
                     });
            });
   };

   get markLocalAvailable(): boolean {
      if (!this.selectedDisk || this.claimableState !== ClaimableState.NOT_USABLE) {
         return false;
      }
      return this.selectedDisk.driveLocality !== DiskLocalityType.Local;
   }

   get markRemoteAvailable(): boolean {
      if (!this.selectedDisk || this.claimableState !== ClaimableState.NOT_USABLE) {
         return false;
      }
      return this.selectedDisk.driveLocality !== DiskLocalityType.Remote;
   }

   markLocal = (local: boolean): void => {
      new ModalBuilder("diskManagement/markLocal")
            .setSize(576, 160)
            .open(local)
            .then(result => {
               this.diskGroupSvc.setDiskLocality(this.host.hostRef, [this.selectedDisk.uuid], local)
                     .then((tasks: ManagedObject[]) => {
                        this.diskManagementActionAvailabilityService.taskStarted.next(tasks);
                     });
            });
   };

   get erasePartitionsAvailable(): boolean {
      return this.selectedDisk && this.claimableState === ClaimableState.NOT_USABLE;
   }

   get precheckDataMigrationLabel(): string {
      return DiskMgmtDiskUtil.getDataMigrationPrecheckLabel(this.clusterCapabilities);
   }

   get viewStorageObjectsLabel(): string {
      return VsanUiUtils.getString(this.isSelectedStoragePmem()
            ? "vsan.actions.viewDeviceObjects.label"
            : "vsan.actions.viewDiskObjects.label");
   }

   addDisks(): void {
      new ModalBuilder("diskManagement/addDisk")
            .open(this.diskGroup)
            .then((task: ManagedObject) => this.diskManagementActionAvailabilityService.taskStarted.next([task]));
   }

   showViewStorageObjectsAction = (): boolean => this.selectedStorage &&
         !DiskMgmtDiskGroupUtil.isUnusedDiskGroup(this.diskGroup) &&
         this.clusterCapabilities.isObjectIdentitiesSupported;

   showDataMigrationPrecheckAction = (): boolean => this.isVsanClaimedDisk() || this.isVsanMaxClaimedDisk();

   showDeleteVsanDiskAction = (): boolean => !this.autoClaimMode
         && (this.isVsanClaimedDisk() || this.isVsanMaxClaimedDisk());

   showDeleteVsanDirectDiskAction = (): boolean => !this.autoClaimMode
         && !this.isVsanClaimedDisk() && !this.isVsanMaxClaimedDisk();

   isSelectedStoragePmem = (): boolean => this.selectedStorage.type === DiskType.PMEM;

   viewStorageObjects = (): void => this.diskManagementActionAvailabilityService.objectsViewSelected
         .next(new ViewObjectsContext(this.host, this.diskGroup, this.selectedStorage));

   erasePartitions = (): void => {
      const context: any = {
         selectedDevices: this.selectedDisk.disk,
      };
      this.navigationService.invokePlatformAction(PlatformExtension.ERASE_PARTITIONS,
            [ManagedObject.uid(this.host.hostRef)],
            context);
   };

   private getDiskGroupActionData(): DiskGroupActionData[] {
      return this.isVsanMaxClaimedDisk()
            ? []
            : [DiskGroupActionData.fromDiskGroupData(this.diskGroup)];
   }
}