import {Injectable} from "@angular/core";
import {ResultItem} from "@service/client/core/query.data";
import {ClusterComputeResource, HostStorageSystem, HostSystem} from "@service/client/dataservice/data-service-property";
import {DataServiceQueryResult} from "@service/client/dataservice/data-service.data";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {VpxdModelFactory} from "@service/client/vpxd/vpxd-model-factory.service";
import {DataServiceResult} from "@service/data-access.service";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";

// TODO (REPA) After switch to remote plugin:
//  * move to a proper service
//  * create proper UI models for all the data being retrieved. Currently we cannot do so because the data in the local
//  plugin is retrieved from H5C provider which returns its own model.
@Injectable()
export class DisksPropertyRetriever {
   private static readonly ICON_ID: string = "primaryIconId";

   private static readonly MO_PREFIX_HOST: string = "host";
   private static readonly MO_PREFIX_STORAGE_SYSTEM: string = "storageSystem";

   constructor(private dataService: DataServiceClient,
         private vpxdModelFactory: VpxdModelFactory) {
   }

   async getPropertiesForHosts(clusterRef: ManagedObject): Promise<DataServiceResult[]> {
      const hostRefs: ManagedObject[] = await this.dataService
            .getProperty(ClusterComputeResource.host.id, clusterRef);
      const storageSystemRefs: ManagedObject[] = hostRefs.map(this.hostRefToStorateSystemRef);

      let hostPropertiesResult: DataServiceResult[];
      let storageDevicesPropertiesResult: DataServiceResult[];
      [hostPropertiesResult, storageDevicesPropertiesResult] = await Promise.all([
         this.getHostProperties(hostRefs),
         this.getStorageDeviceProperties(storageSystemRefs),
      ]);
      return [...hostPropertiesResult, ...storageDevicesPropertiesResult];
   }

   async getStorageDevicePropertiesByHost(hostRef: ManagedObject): Promise<DataServiceResult[]> {
      return this.getStorageDeviceProperties([this.hostRefToStorateSystemRef(hostRef)]);
   }

   static readonly PROP_VSAN_STORAGE_ADAPTER_DEVICES: string = "vsanStorageAdapterDevices";

   private async getStorageDeviceProperties(storageSystemRefs: ManagedObject[]): Promise<DataServiceResult[]> {
      const storageDevices: DataServiceQueryResult = await this.dataService
            .select(HostStorageSystem.storageDeviceInfo.hostBusAdapter,
                  HostStorageSystem.storageDeviceInfo.scsiLun,
                  HostStorageSystem.storageDeviceInfo.plugStoreTopology.path,
                  HostStorageSystem.storageDeviceInfo.plugStoreTopology.adapter,
                  HostStorageSystem.storageDeviceInfo.plugStoreTopology.target,
                  HostStorageSystem.storageDeviceInfo.plugStoreTopology.device)
            .fromMoRef(...storageSystemRefs)
            .execute();
      return storageDevices.resultItems.map((ri: ResultItem) => {
         const adapters: any[] = ri.get(HostStorageSystem.storageDeviceInfo.hostBusAdapter);
         const luns: any[] = ri.get(HostStorageSystem.storageDeviceInfo.scsiLun);

         // The PlugStoreTopology property contains the mapping between LUNs and other objects
         // https://opengrok.eng.vmware.com/source/xref/main.perforce.1666/bora/vim/vmodl/vim/host/PlugStoreTopology.java#15
         const topologyDevices: any[] = ri.get(HostStorageSystem.storageDeviceInfo.plugStoreTopology.device);
         const topologyAdapters: any[] = ri.get(HostStorageSystem.storageDeviceInfo.plugStoreTopology.adapter);
         const topologyPaths: any[] = ri.get(HostStorageSystem.storageDeviceInfo.plugStoreTopology.path);
         const topologyTargets: any[] = ri.get(HostStorageSystem.storageDeviceInfo.plugStoreTopology.target);

         // LUN-to-TRANSPORT mapping
         // topologyDevice.path[] -> topologyPath.key
         // topologyPath.target -> topologyTargets.key
         // topologyTarget.transport

         // LUN-to-ADAPTER mapping
         // lun.key -> topologyDevices.lun
         // topologyDevices.key -> topologyPath.device
         // topologyPath.adapter -> topologyAdapters.key
         // topologyAdapters.adapter -> adapter.key

         const storageDeviceData: any[] = luns.map(lun => {
            const topologyDevice: any = topologyDevices.find(td => td.lun === lun.key);
            const topologyPath: any = topologyPaths.find(p => p.device === topologyDevice.key);
            const topologyAdapter: any = topologyAdapters.find(ta => ta.key === topologyPath.adapter);
            const adapter: any = adapters.find(a => a.key === topologyAdapter.adapter);
            const topologyTarget: any = topologyTargets.find(t => t.key === topologyPath.target);

            // This model is defined in the H5C code base. We need to stick to it as long as we have the local
            // plugin part supported.
            return {
               underlyingLun: lun,
               transport: this.getTransport(topologyTarget?.transport),
               adapter: adapter.device,
            };
         });

         const storageSystemRef: ManagedObject = ri.resourceObject;
         const hostRef: ManagedObject = this.storageSystemRefToHostRef(storageSystemRef);
         return this.createDataServiceResult(hostRef, DisksPropertyRetriever.PROP_VSAN_STORAGE_ADAPTER_DEVICES, storageDeviceData);
      });
   }

   private async getHostProperties(hostRefs: ManagedObject[]): Promise<DataServiceResult[]> {
      const hostProps: DataServiceQueryResult = await this.dataService
            .select(HostSystem.name, HostSystem.config.product.version)
            .fromMoRef(...hostRefs)
            .execute();
      const result: DataServiceResult[] = [];
      hostProps.resultItems.forEach((ri: ResultItem) => {
         const hostRef: ManagedObject = ri.resourceObject;
         result.push(this.createDataServiceResult(hostRef, DisksPropertyRetriever.ICON_ID, ManagedObjectUtils.getObjectTypeIcon(hostRef)));
         ri.propertyValues.forEach(((value: any, key: string) => {
            result.push(this.createDataServiceResult(hostRef, key, value));
         }));
      });
      return result;
   }

   private storageSystemRefToHostRef(storageSystemRef: ManagedObject): ManagedObject {
      return ManagedObject.create(HostSystem.type,
            storageSystemRef.value.replace(DisksPropertyRetriever.MO_PREFIX_STORAGE_SYSTEM, DisksPropertyRetriever.MO_PREFIX_HOST),
            storageSystemRef.serverGuid);
   }

   private hostRefToStorateSystemRef(mo: ManagedObject): ManagedObject {
      return ManagedObject.create(HostStorageSystem.type,
            mo.value.replace(DisksPropertyRetriever.MO_PREFIX_HOST, DisksPropertyRetriever.MO_PREFIX_STORAGE_SYSTEM),
            mo.serverGuid);
   }

   private createDataServiceResult(obj: ManagedObject, propertyName: string, value: any): DataServiceResult {
      return {
         resourceObject: obj,
         value: value,
         propertyName: propertyName,
      } as DataServiceResult;
   }

   private getTransport(transport: any): string[] {
      if (!transport) {
         return [];
      }

      // https://opengrok.eng.vmware.com/source/xref/vim-clients.h5c-main.perforce.1666/h5c-main/applications/vsphere-client/server/modules-api/src/main/java/com/vmware/vsphere/client/storage/adapters/StorageDeviceTransport.java#37
      const clazz: string = this.vpxdModelFactory.getObjectType(transport);
      switch (clazz) {
         case TransportType.HostFibreChannelOverEthernetTargetTransport:
            return ["FCOETRANSPORT"];
         case TransportType.HostFibreChannelTargetTransport:
            return ["FCTRANSPORT"];
         case TransportType.HostInternetScsiTargetTransport:
            return ["ISCSITRANSPORT"];
         case TransportType.HostParallelScsiTargetTransport:
            return ["PARALLELTRANSPORT"];
         case TransportType.HostBlockAdapterTargetTransport:
            return ["BLOCKTRANSPORT"];
         case TransportType.HostSerialAttachedTargetTransport:
            return ["SASTRANSPORT"];
         case TransportType.HostPcieTargetTransport:
            return ["PCIETRANSPORT"];
         case TransportType.HostRdmaTargetTransport:
            return ["RDMATRANSPORT"];
         case TransportType.HostTcpTargetTransport:
            return ["TCPTRANSPORT"];
         default:
            return [];
      }
   }
}

enum TransportType {
   HostFibreChannelOverEthernetTargetTransport = "HostFibreChannelOverEthernetTargetTransport",
   HostFibreChannelTargetTransport = "HostFibreChannelTargetTransport",
   HostInternetScsiTargetTransport = "HostInternetScsiTargetTransport",
   HostParallelScsiTargetTransport = "HostParallelScsiTargetTransport",
   HostBlockAdapterTargetTransport = "HostBlockAdapterTargetTransport",
   HostSerialAttachedTargetTransport = "HostSerialAttachedTargetTransport",
   HostPcieTargetTransport = "HostPcieTargetTransport",
   HostRdmaTargetTransport = "HostRdmaTargetTransport",
   HostTcpTargetTransport = "HostTcpTargetTransport",
}


