import {Component} from "@angular/core";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {EnumeratedFilterSelectionResolver} from "@component/datagrid/filter/enumerated-filter.component";
import {HostData} from "@generated/host-data";
import {DiskMgmtFilterComponent} from "./disk-mgmt-filter.component";

@Component({
   templateUrl: "disk-mgmt-filter.html",
   selector: "vsan-host-list-filter-state",
   styleUrls: ["disk-mgmt-filter.scss"],
})
export class HostListFilterStateComponent extends DiskMgmtFilterComponent<HostData> {

   readonly comparator: EnumeratedFilterSelectionResolver<HostData, string> =
         new class extends EnumeratedFilterSelectionResolver<HostData, string> {
            isSelected(item: HostData, selectedStateKeys: string[]): boolean {
               return selectedStateKeys.some(state => (state === DiskMgmtHostUtil.getState(item)));
            }
         }();
}