import {HostData} from "@generated/host-data";
import {DiskMgmtHostUtil} from "@util/disk-mgmt-host.util";
import {VsanUiUtils} from "@util/vsan-util";
import {ExportDataExtractor} from "@component/export/export-data-extractor";
import {StorageGroupData} from "../model/storage-group-data";
import {DiskMgmtDiskGroupUtil} from "@util/disk-mgmt-disk-group.util";
import {StorageData} from "@component/diskmgmt/model/storage-data";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {ExportDataSource} from "@component/export/export-data-source";
import {ClrDatagrid} from "@clr/angular";
import {DatagridExportDataSourceAdapter} from "@component/export/adapters/datagrid-export-data-source-adapter";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";

export class DiskMgmtExportConfig {

   static createExportDataSource(datagrid: ClrDatagrid): ExportDataSource {
      return new DatagridExportDataSourceAdapter(datagrid);
   }

   static readonly hostListFileName: string = "disk_mgmt_hosts";

   static hostDataExtractors: ExportDataExtractor[] = [
      {
         label: "vsan.manage.diskManagement.hostList.name",
         value: (host: HostData) => host.name,
      },
      {
         label: "vsan.manage.diskManagement.hostList.health",
         value: (host: HostData) => VsanUiUtils.getString(DiskMgmtHostUtil.getHealthKey(host)),
      },
      {
         label: "vsan.manage.diskManagement.hostList.disksInUse",
         value: (host: HostData) => DiskMgmtHostUtil.getClaimedDisksLabel(host),
      },
      {
         label: "vsan.manage.diskManagement.hostList.state",
         value: (host: HostData) => DiskMgmtHostUtil.getState(host),
      },
      {
         label: "vsan.manage.diskManagement.hostList.capacity",
         value: (host: HostData) => DiskMgmtUtil.stringifyCapacity(host.capacity),
      },
      {
         label: "vsan.manage.diskManagement.hostList.contributesTo",
         value: (host: HostData) =>  DiskMgmtHostUtil.getGroupTypeKeys(host)
               .map(key => VsanUiUtils.getString(key))
               .join(";"),
      },
      {
         label: "vsan.manage.diskManagement.hostList.faultDomain",
         value: (host: HostData) => host.faultDomain,
      },
      {
         label: "vsan.manage.diskManagement.hostList.networkPartitionGroup",
         value: (host: HostData) => host.networkPartitionGroup
            ? VsanUiUtils.getString("vsan.host.list.partitionGroupFormat", host.networkPartitionGroup)
            : "",
      },
   ];

   static readonly storageGroupDetailsFileName: string = "disk_mgmt_storage";

   static getStorageDataExtractors(storageGroup: StorageGroupData): ExportDataExtractor[] {
      return [
         DiskMgmtExportConfig.storageName,
         !DiskMgmtDiskGroupUtil.isUnusedDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageHealth : null,
         DiskMgmtExportConfig.storageCapacity,
         DiskMgmtDiskGroupUtil.isVsanDiskGroup(storageGroup) || DiskMgmtDiskGroupUtil.isVsanMaxSingleTier(storageGroup)
               ? DiskMgmtExportConfig.storageUuid : null,
         DiskMgmtExportConfig.storageDriveType,
         DiskMgmtDiskGroupUtil.isVsanDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageTier : null,
         !DiskMgmtDiskGroupUtil.isUnusedDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageMountState : null,
         DiskMgmtDiskGroupUtil.isUnusedDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageOperationalState : null,
         !DiskMgmtDiskGroupUtil.isPmemDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageLocality : null,
         !DiskMgmtDiskGroupUtil.isPmemDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageDiskAdapter : null,
         !DiskMgmtDiskGroupUtil.isPmemDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageTransportType : null,
         DiskMgmtExportConfig.storagePhysicalLocation,
         DiskMgmtDiskGroupUtil.isVsanDiskGroup(storageGroup) || DiskMgmtDiskGroupUtil.isVsanMaxSingleTier(storageGroup)
               ? DiskMgmtExportConfig.storageFsVersion : null,
         DiskMgmtDiskGroupUtil.isUnusedDiskGroup(storageGroup) ? DiskMgmtExportConfig.storageClaimableState : null,
      ].filter(dataExtractor => !!dataExtractor);
   }

   private static storageName: ExportDataExtractor = {
      label: "vsan.disk.list.name",
      value: (storage: StorageData) => storage.name,
   };

   private static storageHealth: ExportDataExtractor = {
      label: "vsan.disk.list.vsanHealthStatus",
      value: (storage: StorageData) => DiskMgmtDiskUtil.getHealth(storage.diskStatus),
   };

   private static storageCapacity: ExportDataExtractor = {
      label: "vsan.disk.list.capacity",
      value: (storage: StorageData) => DiskMgmtUtil.stringifyCapacity(storage.capacity),
   };

   private static storageUuid: ExportDataExtractor = {
      label: "vsan.disk.list.uuid",
      value: (storage: StorageData) => storage.vsanUuid,
   };

   private static storageDriveType: ExportDataExtractor = {
      label: "vsan.disk.list.driveType",
      value: (storage: StorageData) => DiskMgmtDiskUtil.getDiskType(storage.type),
   };

   private static storageTier: ExportDataExtractor = {
      label: "vsan.disk.list.diskRole",
      value: (storage: StorageData) => DiskMgmtDiskUtil.getLocalizedStorage(storage.claimOption),
   };

   private static storageMountState: ExportDataExtractor = {
      label: "vsan.disk.list.state",
      value: (storage: StorageData) => DiskMgmtDiskUtil.localizeMountedState(storage.diskStatus),
   };

   private static storageOperationalState: ExportDataExtractor = {
      label: "vsan.disk.list.operationalState",
      value: (storage: StorageData) => DiskMgmtDiskUtil.localizeOperationalState(storage.deviceState),
   };

   private static storageLocality: ExportDataExtractor = {
      label: "vsan.disk.list.driveLocality",
      value: (storage: StorageData) => DiskMgmtDiskUtil.localizeLocality(storage.driveLocality),
   };

   private static storageDiskAdapter: ExportDataExtractor = {
      label: "vsan.disk.list.diskAdapter",
      value: (storage: StorageData) => storage.diskAdapter,
   };

   private static storageTransportType: ExportDataExtractor = {
      label: "vsan.disk.list.transportType",
      value: (storage: StorageData) =>  DiskMgmtDiskUtil.getTransportTypeText(storage.transportType),
   };

   private static storagePhysicalLocation: ExportDataExtractor = {
      label: "vsan.disk.list.physicalLocation",
      value: (storage: StorageData) =>  DiskMgmtDiskUtil.getPhysicalLocation(storage.physicalLocation),
   };

   private static storageFsVersion: ExportDataExtractor = {
      label: "vsan.disk.list.fileSystemVersion",
      value: (storage: StorageData) =>  DiskMgmtDiskUtil.getFormatVersion(storage.diskStatus),
   };

   private static storageClaimableState: ExportDataExtractor = {
      label: "vsan.disk.list.claimableState",
      value: (storage: StorageData) =>  DiskMgmtDiskUtil.localizeClaimableState(storage.claimableState),
   };
}