import {Component, HostBinding, OnInit} from "@angular/core";
import {VsanBreadcrumbService} from "./vsan-breadcrumb.service";
import {BreadcrumbRouteModel} from "./breadcrumb-route.model";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "vsan-breadcrumb-nav",
   templateUrl: "vsan-breadcrumb-nav.html",
   styleUrls: ["vsan-breadcrumb-nav.scss"],
})
export class VsanBreadcrumbNavComponent implements OnInit {
   routes: BreadcrumbRouteModel[] = [];

   @HostBinding("attr.aria-label") ariaLabel: string = VsanUiUtils.getString("vsan.common.breadcrumb.ariaLabel");

   ngOnInit(): void {
      VsanBreadcrumbService.route.subscribe(route => this.routeTo(route, false));
   }

   onSelect(toRoute: BreadcrumbRouteModel, selectedValue: string): void {
      if (this.isLinkDisabled(toRoute, selectedValue)) {
         return;
      }
      toRoute.select(selectedValue);
      this.routeTo(toRoute, true);
   }

   isLinkDisabled = (route: BreadcrumbRouteModel, link: string): boolean =>
         route && route.isDisabled && route.isDisabled(link);

   private isNextRoute(newlyAddedRoute: BreadcrumbRouteModel): boolean {
      const routesOrder: string[] = VsanBreadcrumbService.getRouteOrders();
      const newlyAddedRouteIndex: number = routesOrder.indexOf(newlyAddedRoute.key);
      if (this.routes.length === 0) {
         return newlyAddedRouteIndex === 0;
      }

      const lastAddedRoute: BreadcrumbRouteModel = this.routes[this.routes.length - 1];
      const lastAddedRouteIndex: number = routesOrder.indexOf(lastAddedRoute.key);
      return newlyAddedRouteIndex - lastAddedRouteIndex === 1;
   }

   routeTo(toRoute: BreadcrumbRouteModel, innerEvent: boolean): void {
      const index: number = this.routes.findIndex(route => route.key === toRoute.key);
      if (index === -1) {
         if (this.isNextRoute(toRoute)) {
            this.routes.push(toRoute);
         }
         return;
      }

      this.routes = this.routes.slice(0, index + 1);
      if (innerEvent) {
         // When navigation event is triggered by breadcrumb component, then propagate to all hooked components as well.
         VsanBreadcrumbService.onRouteClick.next(this.routes[this.routes.length - 1]);
      }
   }

   isSelected = (route: BreadcrumbRouteModel, link: string): boolean => route.selectedLink === link;

   isLastRoute = (index: number): boolean => index === this.routes.length - 1;

   getRouteAriaCurrent(index: number): boolean {
      return this.isLastRoute(index) ? true : null;
   }
}
